@import "@styles/vars.scss";


.card-question {
    display: flex;
    flex-direction: column;
    padding: 0 40px 5px 16px;
    margin-bottom: 16px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    transition: border 0.15s ease, padding 0.35s ease, border-radius 0.15s ease, background-color 0.35s ease;

    @media only screen and (max-width: $bp-lg) {
        padding: 0 20px 12px;
        margin-bottom: 24px;
    }

    &:last-of-type{
        margin-bottom: 0;
    }

    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
        padding-bottom: 20px;

        @media only screen and (max-width: $bp-lg) {
            padding-bottom: 16px;
        }
    }

    &__title {
        font-weight: 500;
        letter-spacing: 0.15px;
        color: $blue;
        transition: text-shadow 0.35s ease, color 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            font-size: 18px;
        }
    }

    &__header:hover &__title{
        text-shadow: 0px 0px 1px $blue;
    }

    &__header:hover &__icon{
        fill: $blue;
    }

    &__icon {
        width: 24px;
        height: 24px;
        flex-shrink: 0;
        margin-left: 16px;
        fill: #A2A2A2;
        transition: fill 0.35s ease, transform 0.35s ease;

        @media only screen and (max-width: $bp-md) {
            width: 16px;
            height: 16px;
        }
    }

    &__body {
        overflow: hidden;
        height: 0;
        transition: height 0.35s ease;
    }

    &__body-inner{
        display: flex;
        align-items: center;

        @media only screen and (max-width: $bp-lg) {
            padding: 10px 0 34px;
        }
    }

    &__img-wrapper {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 76px;
        height: 76px;
        flex-shrink: 0;
        margin: 10px 20px 10px 0;

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 50%;
        }

        .icon{
            width: 29px;
            height: 29px;
            position: absolute;
            bottom: 0;
            right: 0;
            z-index: 1;
        }
    }

    &__body-content{
        flex-grow: 1;
        display: flex;
        flex-wrap: wrap;
    }

    &__text {
        font-family: 'Inter';
        line-height: 150%;
        color: #666;

        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
        }
    }

    &__link{
        margin-bottom: 4px;
    }

    &__body-col{
        display: block;
        box-sizing: border-box;
        max-width: 280px;
        padding-left: 32px;
        width: 50%;
        width: calc(50% - 16px);
        margin: 0 0 20px 32px;

        @media only screen and (max-width: $bp-lg) {
            max-width: none;
            width: calc(50% - 8px);
            margin-left: 16px;
            padding-left: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            padding-left: 10px;
        }

        &:nth-of-type(odd){
            margin-left: 0;
        }

        &:nth-last-of-type(1),
        &:nth-last-of-type(2) {
            margin-bottom: 0;
        }

    }




    &.is-active{
        border-radius: 20px;
        padding: 26px 32px 40px;
        background-color: #fff;
        border-bottom-width: 0px;

        @media only screen and (max-width: $bp-lg) {
            padding: 16px 24px;
        }
    }

    &.is-active &__icon{
        transform: rotate(-45deg);
    }

    &.is-active &__title{
        // font-weight: 700;
        color: $black;
        text-shadow: 0px 0px 1px $black;
    }
}