@import "@styles/vars.scss";

.card-event {
    position: relative;
    display: block;
    box-sizing: border-box;
    background-color: #fff;
    border-radius: 20px;
    padding: 18px 16px 18px 24px;

    @media only screen and (max-width: $bp-lg) {
        padding: 18px 22px;
    }
    
    &__inner {
        display: flex;
        flex-wrap: wrap;
        align-items: center; 
        margin-bottom: -30px;

        &>*{
            margin-bottom: 30px;
        }
    }

    &__date {
        text-align: center;
        display: flex;
        flex-direction: column;
        font-family: 'Inter';
        font-weight: 300;
        font-size: 45px;
        line-height: 120%;
        color: #CCCCCC;
        margin-right: 32px;

        @media only screen and (max-width: $bp-lg) {
            margin-right: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-right: 16px;
        }

        span{
            font-weight: 600;
            font-size: 24px;
            line-height: 120%;
            margin-top: -12px;
        }
    }

    &__info {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        flex: 0 1 42%;

        @media only screen and (max-width: $bp-xl) {
            flex: 1 1 70%;
        }
    }

    &__label {
        display: inline-flex;
        align-items: center;
        margin-bottom: 6px;
        font-family: 'Inter';
        font-weight: 600;
        font-size: 14px;
        line-height: 150%;
        color: #000;

        &--online{
            padding: 2px 12px;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            text-align: center;
            border-radius: 20px;
            background-color: $green;
            color: #fff;
        }
        
        @media only screen and (max-width: $bp-lg) {
            display: none;
        }
    }

    &__tooltip {
        margin-left: 8px;
    }

    &__title {
        text-decoration: none;
        font-weight: 700;
        font-size: 18px;
        line-height: 120%;
        color: $blue;
        margin-right: 30px;
        transition: color 0.35s ease;

        &:hover{
            color: $blue-dark;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-right: 20px;
            font-size: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-right: 0;
        }
    }

    &__price-block {
        flex: 1 1 auto;

        @media only screen and (max-width: $bp-xl) {
            margin-right: auto;
            flex-basis: 50%;
        }

        @media only screen and (max-width: $bp-lg){
            flex-basis: 42%;
        }
    }

    &__price {
        position: relative;
        font-family: 'Inter';
        font-size: 30px;
        line-height: 120%;
        color: #000;

        @media only screen and (max-width: $bp-lg) {
            font-size: 25px;
            color: rgba(0, 0, 0, 0.5);
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 22px;
        }

        &--old{
            color: #D9D9D9;
        }
    }

    &__price-label {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateX(25%) rotate(-6deg);
        padding: 6px 14px;
        font-family: 'Inter';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        color: #fff;
        background-color: #F095BA;
        border-radius: 20px;
    }

    &__button {

        @media only screen and (max-width: $bp-lg) {
            height: 36px;
        }

        .icon{
            display: flex;

            @media only screen and (max-width: $bp-lg) {
                display: none;
            }
        }
    }

    &--mod{
        background-color: $blue;
        color: #fff;
        padding-bottom: 34px;
    }

    &--mod &__title{
        color: #fff;
        font-size: 30px;
    }

    &--mod &__date{
        color: #fff;
    }

    &--mod &__label{
        color: rgba(255, 255, 255, 0.5);

        .tooltip{
            fill: #fff;
        }
    }

    &--mod &__info{
        flex-basis: 55%;
    }

    &--mod &__price-block{
        display: inline-flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 10px) rotate(-7deg);
        margin-bottom: 0;
        

        &--old{
            display: none !important;
        }
    }

    &--mod &__price{
        display: inline-flex;
        align-items: center;
        justify-content: center;
        padding: 0 40px;
        height: 56px;
        background-color: #F095BA;
        border-radius: 50px;
        font-weight: 700;
        color: #fff;
    }

    &--mod &__price-label{
        display: none !important;
    }

    &--mod &__button{
        background-color: $yellow;
        color: $blue-dark;
        fill: $blue-dark;
        margin-left: auto;
        
        &:hover{
            color: $yellow;
            fill: $yellow;
        }
    }
}