@import "@styles/vars.scss";

.coockies{
    position: fixed;
    z-index: 30;
    top: 100%;
    left: 0;
    right: 0;   
    width: 100%;
    padding: 36px 0 52px;
    background-color: $blue;
    transition: transform 0.5s ease;

    @media only screen and (max-width: $bp-lg){
        padding: 24px 0 32px;
    }

    &.is-open{
        transform: translateY(-100%);
    }

    &__container{
        max-width: 1260px;

        @media only screen and (max-width: $bp-lg){
            padding: 0 32px;
        }

        @media only screen and (max-width: $bp-md){
            padding: 0 16px;
        }
    }

    &__inner{
        display: flex;
        align-items: flex-end;
        justify-content: space-between;

        @media only screen and (max-width: $bp-lg){
            align-items: center;
        }

        @media only screen and (max-width: $bp-md){
            flex-direction: column;
            align-items: center;
        }
    }

    &__text{
        font-weight: 500;
        line-height: 150%;
        color: #fff;

        @media only screen and (max-width: $bp-lg){
            font-size: 18px;
        }

        @media only screen and (max-width: $bp-md){
            font-size: 16px;
        }
    }

    &__button{
        margin-left: 80px;

        @media only screen and (max-width: $bp-lg){
            margin-left: 40px;
        }

        @media only screen and (max-width: $bp-md){
            margin-top: 24px;
            margin-left: 0;
        }
    }
}