@import '@styles/vars.scss';

.card-program {
    display: flex;
    flex-direction: column;
    border-radius: 20px;
    background-color: #fff;
    padding: 26px 26px 26px 44px;
    transition: margin 0.35s ease;

    @media only screen and (max-width: $bp-lg) {
        padding-right: 14px;
        padding-left: 14px;
    }

    @media only screen and (max-width: $bp-md) {
        padding-bottom: 40px;
    }

    &__header {
        display: flex;
        align-items: center;
        transition: margin 0.5s ease;

        @media only screen and (max-width: $bp-lg) {
            padding-right: 4px;
            padding-left: 18px;
            flex-wrap: wrap;
        }
    }

    &.is-active &__header{
        margin-bottom: 32px;
    }

    &__header-main {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        flex-grow: 1;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 10px;
            width: 100%;
        }
    }

    &__module {
        margin-bottom: 5px;
        margin-right: 24px;
        color: $gray-dark;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 10px;
            width: 100%;
            margin-right: 0;
        }
    }

    &__header-time {
        display: flex;
        align-items: center;
        margin-bottom: 5px;
        color: $gray-dark;
        fill: $gray-dark;

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }

        .icon{
            width: 22px;
            height: 22px;
            flex-shrink: 0;
            margin-right: 6px;
        }
    }

    &__title {
        width: 100%;
    }

    &__date {
        flex-shrink: 0;
        text-align: right;
        font-weight: 300;
        font-family: 'Inter';
        margin-right: 26px;

        @media only screen and (max-width: $bp-lg) {
            margin-right: 16px;
        }
        
        span{
            font-size: 20px;
        }
    }

    &__button {
        flex-shrink: 0;
        width: 45px;
        height: 45px;
        padding: 0;

        @media only screen and (max-width: $bp-lg) {
            margin-left: auto;
            width: 40px;
            height: 40px;
            border-width: 2px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 32px;
            height: 32px;
        }
    }

    &__body {
        height: 0;
        overflow: hidden;
        transition: height 0.5s ease;
    }

    &.is-active &__content{
        border-top-color: rgba(0, 0, 0, 0.1);
    }

    &__content {
        padding-top: 32px;
        border-top: 1px solid transparent;
        display: flex;
        justify-content: space-between;
        transition: border-color 0.2s ease;

        @media only screen and (max-width: $bp-lg) {
            padding-top: 22px;
            flex-direction: column;
        }
    }

    &__text {
        color: #A3A3A3;
        line-height: 150%;
    }

    &__info {
        width: 216px;
        flex-shrink: 0;
        display: flex;
        flex-direction: column;
        margin-left: 26px;

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            margin-top: 24px;
            margin-left: 0;
        }
    }

    &__time {
        display: flex;
        align-items: center;
        margin-bottom: 16px;
        fill: #A3A3A3;
        color: $gray-dark;

        .icon{
            height: 42px;
            width: 42px;
            margin-right: 14px;

            @media only screen and (max-width: $bp-lg) {
                width: 32px;
                height: 32px;
                margin-right: 24px;
            }

            @media only screen and (max-width: $bp-md) {
                width: 20px;
                height: 20px;
            }
        }

        span{
            
            @media only screen and (max-width: $bp-lg) {
                font-weight: 400;
            }

            br{
                @media only screen and (max-width: $bp-lg) {
                    display: none;
                }
            }
        }
    }

    &__body-button {
        margin-top: auto;
        justify-content: space-between;

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }
    }
}