@import "@styles/vars.scss";

.resolution{
    display: block;
    padding: 26px 54px;
    border: 10px solid $blue;

    @media only screen and (max-width: $bp-xl) {
        padding: 20px 32px;
    }

    @media only screen and (max-width: $bp-lg) {
        padding: 16px 20px;
    }

    @media only screen and (max-width: $bp-md) {
        padding: 12px 10px;
        border-width: 5px;
    }

    &__inner{
        &::after{
            content: "";
            display: block;
            clear: both;
        }

        @media only screen and (max-width: $bp-xl) {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
        }
    }  

    &__img{
        width: 156px;
        height: 156px;
        object-fit: cover;
        flex-shrink: 0;
        margin: 22px 54px 22px 0;
        float: left;

        @media only screen and (max-width: $bp-xl) {
            margin: 0 32px 0 0;
            width: 120px;
            height: 120px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 90px;
            height: 90px;
            margin-right: 20px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 70px;
            height: 70px;
            margin-right: 10px;
        }
    }

    &__title{
        float: right;
        width: calc(100% - 210px);

        @media only screen and (max-width: $bp-xl) {
            width: calc(100% - 152px);
            font-size: 18px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: calc(100% - 110px);
            font-size: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            width: calc(100% - 80px);
            font-size: 14px;
        }
    }

    &__text{
        line-height: 150%;
        float: right;
        width: calc(100% - 210px);
        margin-top: 28px;

        @media only screen and (max-width: $bp-xl) {
            float: left;
            width: 100%;
            margin-top: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
        }
    }
}