@import '@styles/vars.scss';

.course-audience {
    padding: 60px 0 30px;

    @media only screen and (max-width: $bp-xl){
        padding-top: 45px;
    }

    @media only screen and (max-width: $bp-lg){
        padding-top: 30px;
    }

    &__inner {
        padding: 0 55px;

        @media only screen and (max-width: $bp-xl) {
            padding: 0;
        }
    }

    &__title {
        margin-bottom: 60px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 50px;
        }
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin: 0 -30px -55px;

        @media only screen and (max-width: $bp-xl) {
            margin-bottom: -35px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin: 0 0 -22px;
        }
    }

    &__item {
        width: 50%;
        box-sizing: border-box;
        display: flex;
        padding: 0 30px;
        margin-bottom: 55px;

        @media only screen and (max-width: $bp-xl) {
            margin-bottom: 35px;
        }

        @media only screen and (max-width: $bp-lg) {
            align-items: center;
            width: 100%;
            padding: 0;
            margin-bottom: 22px;
        }

    }

    &__item-img {
        flex-shrink: 0;
        width: 126px;
        height: 175px;
        margin-right: 60px;
        object-fit: contain;

        @media only screen and (max-width: $bp-xl) {
            margin-right: 40px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 80px;
            height: 110px;
            margin-right: 24px;
        }
    }

    &__info {
    }

    &__item-title {
        margin-bottom: 24px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 16px;
            font-size: 22px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
        }
    }

    &__item-text {
        color: $gray-dark;

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }
    }
}