@import "@styles/vars.scss";

.teacher {
    padding: 80px 0 120px;
    background-color: #fff;

    @media only screen and (max-width: $bp-xl){
        padding: 60px 0;
    }

    @media only screen and (max-width: $bp-lg){
        padding: 45px 0;
    }

    @media only screen and (max-width: $bp-md){
        padding: 35px 0;
    }

    &__inner {
    }

    &__intro {
        display: flex;
        justify-content: space-between;

        @media only screen and (max-width: $bp-lg){
            flex-direction: column;
            justify-content: flex-start;
        }
    }

    &__intro-main {
    }

    &__intro-subtitle {
        margin-bottom: 4px;

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 6px;
            font-size: 18px;
        }
    }

    &__intro-title {
        margin-bottom: 20px;

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 16px;
        }

        @media only screen and (max-width: $bp-md){
            font-size: 36px;
        }
    }

    &__intro-text {
        color: $gray;
        
        @media only screen and (max-width: $bp-lg){
            font-size: 14px;
        }
    }

    &__achievement {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        width: 100%;
        padding: 18px 16px 20px 20px;
        border-radius: 10px;
        margin-top: 36px;
        background-color: rgba(58, 179, 254, 0.1);

        @media only screen and (max-width: $bp-lg){
            margin-top: 25px;
            padding: 18px 12px;
        }
    }

    &__achievement-icon {
        width: 64px;
        height: 64px;
        flex-shrink: 0;
        margin-right: 20px;

        @media only screen and (max-width: $bp-lg){
            width: 58px;
            height: 58px;
        }

        img{
            width: 100%;
            height: 100%;
        }
    }

    &__achievement-text {
        color: #767B80;

        @media only screen and (max-width: $bp-lg){
            font-size: 12px;
        }
    }

    &__intro-second {
        position: relative;
        margin-left: 160px;

        @media only screen and (max-width: $bp-xxl){
            margin-left: 90px;
        }

        @media only screen and (max-width: $bp-xl){
            margin-left: 60px;
        }

        @media only screen and (max-width: $bp-lg){
            order: -1;
            margin-left: 0;
            width: 100%;
            margin-bottom: 32px;
        }

        @media only screen and (max-width: $bp-md){
            margin-bottom: 25px;
        }
    }

    &__intro-img {
        flex-shrink: 0;
        width: 415px;
        height: 452px;
        border-radius: 20px;
        overflow: hidden;

        @media only screen and (max-width: $bp-xl){
            width: 360px;
            height: 392px;
        }

        img{
            object-fit: cover;
            width: 100%;
            height: 100%;
        }

        @media only screen and (max-width: $bp-lg){
            width: 100%;
            height: auto;
            aspect-ratio: 3/2;
        }
    }

    &__video-link {
        position: absolute;
        bottom: 25px;
        left: 0;
        transform: translateX(-50%);
        width: 95px;
        height: 95px;

        @media only screen and (max-width: $bp-xl){
            width: 80px;
            height: 80px;
        }

        @media only screen and (max-width: $bp-lg){
            bottom: -15px;
            left: auto;
            right: 0;
            transform: translateX(50%);
        }

        @media only screen and (max-width: $bp-md){
            transform: none;
            right: -8px;
        }

        img{
            width: 100%;
            height: 100%;
        }
    }

    &__education {
        box-sizing: border-box;
        display: flex;
        margin-top: 80px;
        padding-top: 75px;
        border-top: 2px solid #C9EBFF;

        @media only screen and (max-width: $bp-xl){
            flex-direction: column;
            padding-top: 50px;
            margin-top: 50px;
        }

        @media only screen and (max-width: $bp-lg){
            margin-top: 80px;
            padding-top: 0;
            border-top: none;
        }
    }

    &__education-title {
        margin-right: 90px;

        @media only screen and (max-width: $bp-xl){
            margin-right: 0;
            margin-bottom: 32px;
        }

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 25px;
        }
    }

    &__education-list {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-bottom: -60px;

        @media only screen and (max-width: $bp-xl){
            margin-bottom: -40px;
        }

        @media only screen and (max-width: $bp-lg){
            margin-bottom: -28px;
        }
    }

    &__education-item {
        display: flex;
        margin-bottom: 60px;
        width: calc(50% - 40px);

        @media only screen and (max-width: $bp-xl){
            width: calc(50% - 20px);
            margin-bottom: 40px;
        }

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 28px;
        }

        @media only screen and (max-width: $bp-md){
            width: 100%;
        }
    }

    &__education-item-title {
        color: #000;
        line-height: 150%;
        margin-right: 18px;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-lg){
            margin-right: 25px;
            font-size: 16px;
        }
    }

    &__education-item-text {
        font-weight: 700;
        line-height: 150%;
        color: $blue;

        @media only screen and (max-width: $bp-lg){
            font-size: 14px;
        }

        span{
            font-weight: 400;
            color: #828282;
        }
    }
}