@import "@styles/vars.scss";

.teacher-articles{
    padding: 60px 0;

    @media only screen and (max-width: $bp-lg){
        padding: 40px 0;
    }

    &__title{
        margin-bottom: 40px;

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 30px;
        }
    }

    &__list{
        margin-bottom: -40px;

        @media only screen and (max-width: $bp-lg){
            margin-bottom: -32px;
        }

        @media only screen and (max-width: $bp-md){
            margin-bottom: -25px;
        }

        .card-article{
            margin-bottom: 40px;

            @media only screen and (max-width: $bp-lg){
                margin-bottom: 32px;
            }

            @media only screen and (max-width: $bp-md){
                margin-bottom: 25px;
            }
        }
    }
}