@import "@styles/vars.scss";

.article {
    padding: 30px 0 60px;

    &--lg &__container{

        @media only screen and (min-width: $bp-xl + 1){
            padding: 0 60px;
        }

        @media only screen and (min-width: $bp-lg + 1){
            padding: 0 40px;
            max-width: 1430px;
        }
    }

    &--lg &__aside{
        @media only screen and (max-width: $bp-lg){
            display: none;
        }
    }

    &__header{

    }

    h2, h3, h4, h5{
        display: inline-flex;
        font-family: 'Raleway';
        font-weight: 700;
        letter-spacing: 0.15px;
    }

    h2{
        font-size: 50px;
        line-height: 120%;

        width: 100%;
        max-width: calc(100% - 430px);
        margin-bottom: 40px;

        @media only screen and (max-width: $bp-xl){
            max-width: calc(100% - 360px);
        }

        @media only screen and (max-width: $bp-lg) {
            max-width: none;
            margin-bottom: 32px;
            font-size: 38px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 24px;
            font-size: 24px;
        }
    }

    h3{
        font-size: 30px;
        line-height: 120%;
        margin-bottom: 40px;
    
        @media only screen and (max-width: $bp-lg) {
            font-size: 24px;
            margin-bottom: 32px;
        }
    
        @media only screen and (max-width: $bp-md) {
            font-size: 20px;
            margin-bottom: 24px;
        }
    }

    h4{
        font-size: 25px;
        line-height: 120%;
    }

    h5{
        font-size: 20px;
        line-height: 120%;
    }

    p{
        display: inline-block;
        font-family: 'Inter';
        font-size: 18px;
        line-height: 150%;

        @media only screen and (max-width: $bp-md){
            font-size: 16px;
        }

        a{
            display: inline;
            align-items: center;
            text-decoration: none;
            font-weight: 700;
            color: $blue;
            fill: $blue;
            transition: fill 0.35s ease, color 0.35s ease;
    
            &:hover{
                color: $blue-dark;
                fill: $blue-dark;
            }
        }
    }

    ul{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin: 0;
        padding: 0 0 0 40px;
        
        @media only screen and (max-width: $bp-lg){
            padding-left: 30px;
        }

    
        li{
            box-sizing: border-box;
            position: relative;
            display: inline-flex;
            align-items: flex-start;
            padding-left: 30px;
            margin-bottom: 20px;
            width: calc(50% - 15px);
            
            @media only screen and (max-width: $bp-md){
                width: 100%;
            }

            &:last-of-type{
                margin-bottom: 0;
            }

            &::after{
                content: "";
                display: inline-flex;
                width: 18px;
                height: 1px;
                background-color: $black;
                position: absolute;
                top: 14px;
                left: 0;
            }

            p{

            }
        }
    }

    ol{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-left: 0;
        text-align: center;
        counter-reset: item;
        list-style-type: none;

        li{
            display: inline-flex;
            align-items: center;
            margin-bottom: 30px;
            text-align: left;

            &::before{
                min-width: 60px;
                content: counters(item, ".") ".";
                counter-increment: item;
                font-family: 'Inter';
                font-weight: 700;
                font-size: 64px;
                line-height: 120%;
                color: $blue;
                margin-right: 25px;
                
                @media only screen and (max-width: $bp-lg){
                    margin-right: 16px;
                }
            }
            
            &:last-of-type{
                margin-bottom: 0;
            }
            
            p{
                max-width: 400px;

                @media only screen and (max-width: $bp-lg){
                    max-width: none;
                }
            }
        }
    }

    &__header{
        box-sizing: border-box;
        position: relative;
        display: flex;
        align-items: flex-end;
        min-height: 485px;
        margin-bottom: 64px;
        padding: 42px 54px;

        @media only screen and (max-width: $bp-xl){
            padding: 32px 42px;
        }

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 50px;
            padding: 24px 32px;
            min-height: 375px;
        }

        @media only screen and (max-width: $bp-md){
            padding: 18px;
        }
    }

    &__header-title{
        position: relative;
        z-index: 2;
        color: #fff;
        max-width: 675px;

        h1{
            font-family: 'Raleway';
            font-weight: 700;
            font-size: 50px;
            line-height: 120%;
            letter-spacing: 0.15px;
            
            @media only screen and (max-width: $bp-lg){
                font-size: 38px;
            }
            
            @media only screen and (max-width: $bp-md){
                font-size: 32px;
            }
        }

        @media only screen and (max-width: $bp-lg){
            max-width: none;
        }
    }

    &__header-img{
        display: block;
        width: 100%;
        height: 100%;
        border-radius: 30px;
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;

        &::after{
            content:"";
            position: absolute;
            top: 0;
            right: 0;
            z-index: 1;
            display: block;
            width: 100%;
            height: 100%;
            background: linear-gradient(360deg, rgba(21, 127, 193, 0.7) 8.02%, rgba(58, 179, 254, 0) 80.17%);
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__inner {
        padding-left: 60px;

        @media only screen and (max-width: $bp-xl){
            padding-left: 0;
        }
    }

    &__group {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 70px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 60px;
        }

        &:last-of-type{
            margin-bottom: -50px;
        }

    }

    &__block {
        display: block;
        box-sizing: border-box;
        width: 100%;
        max-width: calc(100% - 430px);
        margin: 0 60px 50px 0;

        @media only screen and (max-width: $bp-xl){
            margin-right: 40px;
            max-width: calc(100% - 360px);
        }

        @media only screen and (max-width: $bp-lg){
            max-width: none;
            margin-right: 0;
            margin-bottom: 40px;
        }
        
        p{
            color: $gray-dark; 
            margin-bottom: 30px;

            @media only screen and (max-width: $bp-lg) {
                margin-bottom: 25px;
            }

            &:last-of-type{
                margin-bottom: 0;
            }
        }
    }

    &__aside {
        width: 370px;
        margin-left: auto;

        @media only screen and (max-width: $bp-xl){
            width: 320px;
        }

        @media only screen and (max-width: $bp-lg){
            width: 100%;
            margin-bottom: 40px;
        }
    }

    &__aside-content{
        box-sizing: border-box;
        display: flex;
        align-items: center;
        padding-bottom: 30px;
        padding: 0 15px 30px;
        border-bottom: 5px solid $blue;
        flex-wrap: wrap;

        @media only screen and (max-width: $bp-xl){
            padding: 0 0 20px;
        }

        @media only screen and (max-width: $bp-lg){
            border-bottom: none;
            padding-bottom: 0;
        }

        h2{
            align-items: flex-end;
            color: $blue;
            max-width: none;
            margin-bottom: 18px;

            strong{
                font-family: 'Inter';
                font-size: 100px;
                line-height: 100%;
                margin-right: 12px;
            }
        }

        p{
            font-weight: 700;
            line-height: 120%;

            @media only screen and (max-width: $bp-xl){
                font-size: 16px;
            }

            @media only screen and (max-width: $bp-lg){
                font-size: 18px;
            }
        }

        img{
            width: 100px;
            height: 100px;
            margin-right: 10px;
            flex-shrink: 0;

            @media only screen and (max-width: $bp-xl){
                width: 80px;
                height: 80px;
            }

            @media only screen and (max-width: $bp-lg){
                width: 100px;
                height: 100px;
                margin-right: 16px;
            }
        }

        img + p{
            width: calc(100% - 110px);

            @media only screen and (max-width: $bp-xl){
                width: calc(100% - 90px);
            }

            @media only screen and (max-width: $bp-lg){
                width: calc(100% - 116px);
            }
        }
    }

    
    &__author {
        display: flex;
        flex-direction: column;
        width: 275px;
        margin-left: auto;

        @media only screen and (max-width: $bp-lg){
            width: 100%;
            margin-left: 0;
            flex-direction: row;
            align-items: center;
            margin-bottom: 80px;
        }

        p{
            font-size: 14px;
            line-height: 120%;
            margin-bottom: 2px;

            @media only screen and (max-width: $bp-lg){
                margin-bottom: 4px;
            }

            @media only screen and (max-width: $bp-md){
                font-size: 12px;
            }
        }

        a{
            display: inline-flex;
            align-items: center;
            text-decoration: none;
            color: $blue;
            fill: $blue;
            transition: fill 0.35s ease, color 0.35s ease;
    
            &:hover{
                color: $blue-dark;
                fill: $blue-dark;
            }
        }

        h4{
            @media only screen and (max-width: $bp-lg){
                font-size: 20px;
            }

            @media only screen and (max-width: $bp-md){
                font-size: 16px;
            }
        }
    }

    &__author-img {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 182px;
        height: 182px;
        flex-shrink: 0;
        border-radius: 50%;
        overflow: hidden;
        margin: 0 auto 14px;

        @media only screen and (max-width: $bp-lg){
            width: 60px;
            height: 60px;
            margin: 0 16px 0 0 ;
        }

        @media only screen and (max-width: $bp-md){
            width: 46px;
            height: 46px;
            margin-right: 10px;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__author-main{
        display: flex;
        flex-direction: column;
    }


    &__block-info {
        width: 100%;
        padding: 0;
        margin-bottom: 50px;

        .container{
            padding: 0 !important;
        }

        .block-info__inner{
            padding: 100px 60px;

            @media only screen and (max-width: $bp-xl) {
                padding: 75px 50px;
            }
    
            @media only screen and (max-width: $bp-lg) {
                padding: 50px 0;
            }
        }

        p{
            font-size: 30px;
            line-height: 120%;
            letter-spacing: 0.15px;
        
            @media only screen and (max-width: $bp-lg) {
                font-size: 24px;
            }
        
            @media only screen and (max-width: $bp-md) {
                font-size: 20px;
            }
        }
    }

    &__border {
        box-sizing: border-box;
        width: 100%;
        border: 10px solid $blue;
        padding: 36px;
        display: block;

        @media only screen and (max-width: $bp-lg){
            padding: 32px 24px;
        }

        @media only screen and (max-width: $bp-md){
            padding: 32px 18px;
        }

        p{
            color: #000;
            font-weight: 700;
            font-size: 18px;
            line-height: 150%;
            
        }

        &--fill{
            border: none;
            padding: 45px 28px;
            border-radius: 20px;
            background-color: $blue;

            @media only screen and (max-width: $bp-lg){
                padding: 28px 25px 35px;
            }

            p{
                color: #fff;

                @media only screen and (max-width: $bp-md){
                    font-size: 16px;
                }
            }
        }
    }

    blockquote{
        padding: 0;
        margin: 0;
        position: relative;
        padding: 0 16px 35px 70px;

        @media only screen and (max-width: $bp-xl){
            padding-left: 45px;
        }

        @media only screen and (max-width: $bp-lg){
            padding: 42px 16px 32px;
            
        }

        @media only screen and (max-width: $bp-md){
            padding: 42px 10px 32px;
        }

        &::before, 
        &::after{
            pointer-events: none;
            content: "";
            display: block;
            width: 32px;
            height: 32px;
            background-size: 100%;
            background-repeat: no-repeat;
            position: absolute;
            z-index: 2;

            @media only screen and (max-width: $bp-lg){
                width: 24px;
                height: 24px;
            }

            @media only screen and (max-width: $bp-md){
                width: 20px;
                height: 20px;
            }
        }
    
        &::before{
            top: 0;
            left: 0;
            background-image: url("data:image/svg+xml,%3Csvg width='22' height='22' viewBox='0 0 22 22' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.929688 10.4258L9.23047 0.44043L12.1357 2.10059L5.12891 10.7188L12.1357 19.3369L9.23047 21.0215L0.929688 11.085V10.4258ZM10.6465 10.4258L19.0693 0.44043L21.9258 2.10059L14.9434 10.7188L21.9258 19.3369L19.0693 21.0215L10.6465 11.085V10.4258Z' fill='%23CCCCCC'/%3E%3C/svg%3E%0A");
        }

        &::after{
            bottom: 0;
            right: 0;
            transform: translateX(50%);

            @media only screen and (max-width: $bp-lg){
                transform: none;
            }
            background-image: url("data:image/svg+xml,%3Csvg width='22' height='21' viewBox='0 0 22 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M21.0703 11.0146L12.7695 21L9.86426 19.3398L16.8711 10.7217L9.86426 2.10351L12.7695 0.418945L21.0703 10.3555L21.0703 11.0146ZM11.3535 11.0146L2.93066 21L0.0742189 19.3398L7.05664 10.7217L0.0742204 2.10351L2.93067 0.418944L11.3535 10.3555L11.3535 11.0146Z' fill='%23CCCCCC'/%3E%3C/svg%3E%0A");
        }

        p, h3 {
            font-weight: 300;
            margin-bottom: 0;

            @media only screen and (max-width: $bp-lg){
                font-size: 25px;
            }

            @media only screen and (max-width: $bp-md){
                font-size: 25px;
            }
        }
    }

    &__border--fill {
    }

    &__slider {
        .slider {

            &__inner {
            }
        
            &__wrapper {
            }
        
            &__slide {
            }
        
            &__navigation {

                @media only screen and (max-width: $bp-lg){
                    display: none;
                }
            }
        
            &__button {
                margin-right: 16px;

                &:last-of-type{
                    margin-right: 0;
                }
            }
        
            &__button--prev {
            }
        
            &__button-icon {
            }
        
            &__button--next {
            }
        
            &__pagination {
                display: none;

                @media only screen and (max-width: $bp-lg){
                    display: flex;
                }
            }
        }
    }

    &__media {
        display: flex;
        align-items: center;
        justify-content: center;
        aspect-ratio: 1.93;
        border-radius: 20px;
        overflow: hidden;

        a{
            cursor: pointer;
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;

            &::after{
                content:"";
                display: block;
                width: 82px;
                height: 82px;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                background-size: 100%;
                background-repeat: no-repeat;
                background-image: url("data:image/svg+xml,%3Csvg width='83' height='83' viewBox='0 0 83 83' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M41.5 0.8125C19.0311 0.8125 0.8125 19.0311 0.8125 41.5C0.8125 63.9689 19.0311 82.1875 41.5 82.1875C63.9689 82.1875 82.1875 63.9689 82.1875 41.5C82.1875 19.0311 63.9689 0.8125 41.5 0.8125ZM54.5872 42.1267L34.7521 56.558C34.6433 56.6361 34.5151 56.6828 34.3816 56.6928C34.2481 56.7029 34.1144 56.6759 33.9952 56.6149C33.876 56.5539 33.7759 56.4612 33.7059 56.347C33.636 56.2328 33.5989 56.1016 33.5986 55.9677V27.1231C33.5982 26.989 33.6349 26.8573 33.7047 26.7427C33.7745 26.6281 33.8747 26.5351 33.9942 26.474C34.1136 26.4129 34.2476 26.386 34.3814 26.3964C34.5152 26.4068 34.6435 26.454 34.7521 26.5328L54.5872 40.9551C54.6809 41.0214 54.7574 41.1091 54.8101 41.2111C54.8629 41.313 54.8904 41.4261 54.8904 41.5409C54.8904 41.6556 54.8629 41.7687 54.8101 41.8707C54.7574 41.9726 54.6809 42.0604 54.5872 42.1267Z' fill='%233AB3FE'/%3E%3Cpath d='M54.5872 42.1267L34.7521 56.558C34.6433 56.6361 34.5151 56.6828 34.3816 56.6928C34.2481 56.7029 34.1144 56.6759 33.9952 56.6149C33.876 56.5539 33.7759 56.4612 33.7059 56.347C33.636 56.2328 33.5989 56.1016 33.5986 55.9677V27.1231C33.5982 26.989 33.6349 26.8573 33.7047 26.7427C33.7745 26.6281 33.8747 26.5351 33.9942 26.474C34.1136 26.4129 34.2476 26.386 34.3814 26.3964C34.5152 26.4068 34.6435 26.454 34.7521 26.5328L54.5872 40.9551C54.6809 41.0214 54.7574 41.1091 54.8101 41.2111C54.8629 41.313 54.8904 41.4261 54.8904 41.5409C54.8904 41.6556 54.8629 41.7687 54.8101 41.8707C54.7574 41.9726 54.6809 42.0604 54.5872 42.1267Z' fill='white'/%3E%3C/svg%3E%0A");
                transition: opacity 0.35s ease;

                @media only screen and (max-width: $bp-xl){
                    width: 66px;
                    height: 66px;
                }

                @media only screen and (max-width: $bp-md){
                    width: 55px;
                    height: 55px;
                }
            }

            &:hover {
                &::after{
                    opacity: 0.85;
                }
            }
        }

        img{
            width: 100%;
            height: 100%;
        }
    }

    &__media + p{
        margin-top: 15px;
        padding-left: 50px;

        @media only screen and (max-width: $bp-lg){
            padding-left: 0;
        }
    }

    &__stats{
        h4{
            margin-bottom: 35px;

            @media only screen and (max-width: $bp-lg){
                margin-bottom: 40px;
            }
        }
    }

    &__stats-list{
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        flex-wrap: wrap;
        margin: 0 -20px -20px;

        @media only screen and (max-width: $bp-lg){
            justify-content: space-between;
        }
    }

    &__stats-item{
        box-sizing: border-box;
        flex-shrink: 0;
        display: flex;
        flex-direction: column;
        padding: 0 20px;
        margin-bottom: 20px;

        @media only screen and (max-width: $bp-lg){
            min-width: 50%;
        }

        &:last-of-type{
            margin-right: 0;
        }
        
        span{
            font-family: 'Inter';
            font-weight: 700;
            font-size: 60px;
            line-height: 120%;
            margin-bottom: 8px; 
            color: transparent;
            -webkit-text-stroke: 2px $blue;
        }

        p{
            width: 100%;
            text-align: left;
            font-size: 16px;
            color: #000;
        }
    }

    &__card {
    }

    &__group + &__socials{
        margin-top: -75px;
        
        @media only screen and (max-width: $bp-lg){
            margin-top: -55px;
        }
    }

    &__socials {
        width: 100%;
        margin-top: 45px;
        display: flex;
        align-items: center;

        @media only screen and (max-width: $bp-lg){
            margin-top: 32px;
        }

        @media only screen and (max-width: $bp-md){
            margin-top: 25px;
        }
    }

    &__socials-title {
        color: $black;
        margin-right: 30px;

        @media only screen and (max-width: $bp-lg){
            margin-right: 24px;
            font-size: 16px;
        }

        @media only screen and (max-width: $bp-md){
            font-size: 14px;
        }
    }

    &__socials-list {
        display: flex;
        align-items: center;
    }

    &__socials-item {
        width: 40px;
        height: 40px;
        margin-right: 10px;
        fill: $black;
        transition: fill 0.35s ease;

        @media only screen and (max-width: $bp-lg){
            width: 32px;
            height: 32px;
        }

        @media only screen and (max-width: $bp-md){
            width: 28px;
            height: 28px;
            margin-right: 8px;
        }

        &:hover{
            fill: $blue;
        }

        &:last-of-type{
            margin-right: 0;
        }
    }

    &__socials-icon {
        width: 100%;
        height: 100%;
    }
}

