@import "@styles/vars.scss";

.contacts {

    &__map-wrapper {
    }

    &__map {
        width: 100%;
        height: 282px;

        @media only screen and (max-width: $bp-lg){
            height: 205px;
        }

        iframe{
            width: 100%;
            height: 100%;
        }

        // .mapboxgl-marker,
        // .mapboxgl-marker.mapboxgl-marker-anchor-center{
        //     width: 24px;
        //     height: 24px;
        //     background-size: 100%;
        //     background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M24 12C24 18.6274 18.6274 24 12 24C5.37258 24 0 18.6274 0 12C0 5.37258 5.37258 0 12 0C18.6274 0 24 5.37258 24 12Z' fill='white'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M12 17C14.7614 17 17 14.7614 17 12C17 9.23858 14.7614 7 12 7C9.23858 7 7 9.23858 7 12C7 14.7614 9.23858 17 12 17ZM12 24C18.6274 24 24 18.6274 24 12C24 5.37258 18.6274 0 12 0C5.37258 0 0 5.37258 0 12C0 18.6274 5.37258 24 12 24Z' fill='%233AB3FE'/%3E%3C/svg%3E%0A");

        //     svg{
        //         display: none !important;
        //     }
        // }
    }

    &__main {
        position: relative;
        z-index: 9;
    }

    &__inner {
    }

    &__title {
        font-family: 'Raleway';
        font-weight: 700;
        font-size: 100px;
        line-height: 120%;
        margin-bottom: 24px;
        margin-top: -72px;

        @media only screen and (max-width: $bp-xl){
            font-size: 80px;
            margin-top: -58px;
        }

        @media only screen and (max-width: $bp-lg){
            font-size: 60px;
            margin-top: -42px;
        }

        @media only screen and (max-width: $bp-md){
            font-size: 50px;
            margin-top: -38px;
        }
    }

    &__row {
        display: flex;
        margin-right: -20px;
        margin-left: -20px;
        flex-wrap: wrap;

        @media only screen and (max-width: $bp-md){
            width: 100%;
            margin-right: -10px;
            margin-left: -10px;
        }

        &:last-of-type{
            margin-bottom: -45px;

            @media only screen and (max-width: $bp-lg){
                margin-bottom: -32px;
            }

            @media only screen and (max-width: $bp-md){
                margin-bottom: -20px;
            }
        }
        
        @media only screen and (max-width: $bp-md){
            &::after{
                content:"";
                display: block;
                clear: both;
            }
        }
    }

    &__row--center &__col{
        justify-content: center;
    }

    &__col {
        box-sizing: border-box;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        margin-bottom: 45px;
        padding: 0 20px;
        flex-shrink: 0;
        width: auto;

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 32px;
        }

        @media only screen and (max-width: $bp-md){
            margin-bottom: 25px;
            padding: 0 10px;
            width: 100%;
        }

        @media only screen and (min-width: $bp-md + 1){
                
            &:nth-of-type(1){
                width: 50%;
            }

            &:nth-of-type(2){
                width: 250px;
                width: 50%;
            }

            &:nth-of-type(3){
                flex-shrink: 1;
                flex-grow: 1;
            }
        }

        @media only screen and (min-width: $bp-xl + 1){
            &:nth-of-type(1){
                width: 240px;
            }

            &:nth-of-type(2){
                width: 290px;
            }

            &:nth-of-type(3){
                flex-shrink: 1;
                flex-grow: 1;
            }
        }

        &--req{
            @media only screen and (max-width: $bp-md){
                margin-bottom: 60px;
            }
        }

        &--soc{
            @media only screen and (max-width: $bp-md){
                width: auto;
                order: 2;
            }
        }

        &--actions{
            @media only screen and (max-width: $bp-md){
                min-width: 50%;
                width: auto;
            }
        }
    }
    &__col--req &__text{
        @media only screen and (max-width: $bp-md){
            display: none;
        }
    }

    &__col--req &__link{
        display: none;

        @media only screen and (max-width: $bp-md){
            display: inline-flex;
        }
    }

    &__col--actions &__link{
        @media only screen and (max-width: $bp-md){
            display: none;
        }
    }

    &__col--actions &__col-content{
        @media only screen and (max-width: $bp-md){
            display: flex;
            flex-direction: column;
        }
    }

    &__col-title {
        color: #C1C1C1;
        margin-bottom: 18px;

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 12px;
        }

        @media only screen and (max-width: $bp-md){
            margin-bottom: 6px;
        }
    }

    &__col-content {
    }

    &__link {

        @media only screen and (max-width: $bp-md){
            font-size: 14px;
        }

        &--black{
            color: #000;
            text-decoration: none;

            @media only screen and (max-width: $bp-md){
                font-size: 20px;
            }
        }
    }

    &__text{
        color: #484B4D;

        @media only screen and (max-width: $bp-lg){
            font-size: 14px;
        }
    }

    &__socials {
        display: flex;
        align-items: center;
    }

    &__socials-item {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 32px;
        height: 32px;
        margin-right: 6px;
        fill: #000;
        transition: fill 0.35s ease;

        &:hover{
            fill: $blue;
        }

        &:last-of-type{
            margin-right: 0;
        }

        .icon{
            width: 100%;
            height: 100%;
        }
    }

    &__button {
        margin-right: 65px;

        @media only screen and (max-width: $bp-md){
            margin-right: 0;
            height: 44px;
            padding: 0 22px;
        }

        &:last-child{
            margin-right: 0;
        }
    }
}