@import '@styles/vars.scss';

@import "~bootstrap/scss/functions";
@import "~bootstrap/scss/variables";
@import "~bootstrap/scss/mixins";
@import "~bootstrap/scss/modal";
@import "~bootstrap/scss/transitions";

.fade{
	transition: opacity 0.25s linear;
}
.modal {
	&.fade &-dialog{
		transform: translateY(15px);
		transition: transform 0.25s ease-out;

	}

	&.show &-dialog{
		transform: none;
	}

	&-backdrop{
		&.show{
			background: #000;
			opacity: 0.45;
		}
	}

	&-dialog {
		border-radius: 0;
		border: none;
		max-width: 488px;
		margin-right: auto;
		margin-left: auto;
	}

	&-content {
		box-sizing: border-box;
		border: none;
		background-color: #fff;
		border-radius: 10px;
		min-height: 595px;
		padding: 50px 65px 0;
		border-radius: 44px;
		display: flex;

		@media only screen and (max-width: $bp-lg){
			padding: 50px 45px 0;
			min-height: 488px;
		}

		@media only screen and (max-width: $bp-md){
			padding-right: 32px;
			padding-left: 32px;
		}
	}

	&-close {
		position: absolute;
		right: 32px;
		top: 32px;
		width: 32px;
		height: 32px;
		background-color: transparent;
		display: flex;
		align-items: center;
		justify-content: center;
		border: none;
		cursor: pointer;

		@media only screen and (max-width: $bp-lg){
			top: 22px;
			right: 22px;
		}

		@media only screen and (max-width: $bp-md) {
			width: 24px;
			height: 24px;
		}

		&:hover {
			.icon{
				fill: $blue;
			}
		}

		.icon {
			width: 100%;
			height: 100%;
			transition: fill 0.3s ease;
			fill: $blue-dark;

			@media only screen and (max-width: $bp-md) {
				width: 24px;
				height: 24px;
			}
		}

	}

	&__title{
		font-family: 'Inter';
		font-weight: 700;
		font-size: 40px;
		line-height: 120%;
		margin-bottom: 48px;

		@media only screen and (max-width: $bp-lg){
			font-size: 36px;
		}

		@media only screen and (max-width: $bp-md){
			font-size: 32px;
		}
	}

	&__main{
		display: flex;
		flex-direction: column;
		padding-bottom: 80px;
		flex-grow: 1;

		@media only screen and (max-width: $bp-lg){
			padding-bottom: 54px;
		}

		@media only screen and (max-width: $bp-md){
			padding-bottom: 50px;
		}
	}

	&__form{
		display: flex;
		flex-direction: column;
		flex-grow: 1;

		form{
			display: flex;
			flex-direction: column;
			flex-grow: 1;
		}
	}

	&__input{
		width: 100%;
		margin-bottom: 25px;
	}

	&__button{
		margin-top: auto;
		font-size: 20px;

		@media only screen and (max-width: $bp-lg){
			font-size: 16px;
		}
	}

	&--course &-dialog{
		max-width: 1024px;
	}

	&--course &-content{
		flex-direction: row;
	}

	&--course &__img{
		object-fit: contain;
		margin-left: auto;
		width: calc(100% - 425px);
		max-width: 408px;
		margin-top: auto;

		@media only screen and (max-width: $bp-md){
			display: none;
		}
	}
	&--course &__form form{
		align-items: flex-start;

		@media only screen and (max-width: $bp-md){
			align-items: initial;
		}
	}
	&--course &__main{
		max-width: 375px;

		@media only screen and (max-width: $bp-md){
			max-width: none;
		}
	}

	&--ok &-dialog{
	}
	
	&--ok &-content{
		min-height: 468px;
		padding-top: 70px;
		
		@media only screen and (max-width: $bp-lg){
			min-height: 390px;
		}
	}

	&--ok &__img{
		width: 115px;
		height: 115px;
		order: -1;
		margin: 0 auto 35px;

		@media only screen and (max-width: $bp-md){
			width: 90px;
			height: 90px;
			margin-bottom: 25px;
		}
	}
	
	&--ok &__main{
		align-items: center;
		text-align: center;
	}
	
	&--ok &__title{
		margin-bottom: 18px;
	}
	
}