@import '@styles/vars.scss';


.slider {

    &__navigation {
        position: static;
        display: flex;
        display: inline-flex;
        align-items: center;
        justify-content: flex-start;
        margin-top: 34px;

        @media only screen and (max-width: $bp-xl){
            margin-top: 22px;
        }
        
        @media only screen and (max-width: $bp-lg){
            margin-top: 40px;
        }
        
        @media only screen and (max-width: $bp-md){
            margin-top: 24px;
        }
    } 

    &__button {
        margin: 0;
        box-sizing: border-box;
        position: static;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 45px;
        height: 45px;
        border-radius: 50%;
        flex-shrink: 0;
        border: 2px solid $blue;
        fill: $blue;
        background-color: transparent;
        transition: background-color 0.35s ease, fill 0.35s ease, color 0.35s ease, opacity 0.35s ease;

        &:hover{
            background-color: $blue;
            color: #fff;
            fill: #fff;
        }

        &::after, 
        &::before{
            content: none;
            
        }
    }

    &__button-icon{
        flex-shrink: 0;
        width: 32px;
        height: 32px;
    }
      
    &__pagination{
        position: static;
        width: auto;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 30px;

    }

    &__pagination-item{
        cursor: pointer;
        display: block;
        width: 12px;
        height: 12px;
        margin: 0 4px;
        border-radius: 50%;
        background-color: $gray;
        transition: background-color 0.35s ease;

        @media only screen and (max-width: $bp-lg){
            width: 7px;
            height: 7px;
            margin: 0 2px;
        }

        &.swiper-pagination-bullet-active{
            background-color: $blue;
        }
    }

    &__fraction-item{
        cursor: pointer;
        display: inline-flex;
        align-items: center;
        font-family: 'Inter';
        color: #B9B9B9;
        font-size: 14px;
        line-height: 24px;
        margin-left: 12px;
        transition: font-size 0.15s ease, color 0.35s ease;

        &:hover{
            color: $blue;
        }

        &::after{
            pointer-events: none;
            content:"";
            display: inline-flex;
            margin-left: 0px;
            height: 1px;
            width: 0px;
            background-color: #000;
            opacity: 0.2;
            transition: margin 0.45s ease, width 0.45s ease;
        }

        &--active{
            font-size: 20px;
            color: $blue;

            &::after{
                width: 50px;
                margin-left: 20px;
            }
        }

        &:first-of-type{
            margin-left: 0;
        }

        &:last-of-type::after{
            order: -1;
        }

        &--active:last-of-type{
            &::after{
                margin-left: 0;
                margin-right: 20px;
            }
        }

    }
}
