@import "@styles/vars.scss";

button,
a {
    padding: 0;
    background: none;
    border: none;
    outline: none;
}

.button {
    z-index: 1;
    cursor: pointer;
    padding: 0;
    background: none;
    border: none;
    outline: none;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 54px;
    box-sizing: border-box;
    padding: 0 32px;
    border-radius: 50px;
    text-decoration: none;
    font-weight: 700;
    font-size: 16px;
    line-height: 120%;
    color: #fff;
    fill: #fff;
    background-color: $blue;
    transition: color 0.35s ease, fill 0.35s ease, border-color 0.35s ease, background-color 0.35s ease, opacity 0.35s ease;

    &__text{
        
    }

    &__icon{
        flex-shrink: 0;
        width: 32px;
        height: 32px;
    }

    &__text + &__icon{
        margin-left: 10px;
    }

    &__icon &__text{
        margin-left: 10px;
    }

    &:hover{
        fill: $blue;
        color: $blue;
        background-color: transparent;
    }


    &--border{
        border: 3px solid $blue;
        color: $blue;
        fill: $blue;
        background-color: transparent;

        &:hover{
            background-color: $blue;
            color: #fff;
            fill: #fff;
        }
    }

    &--white{
        background-color: #fff;
        fill: $blue;
        color: $blue;

        &:hover{
            color: #fff;
            fill: #fff;
            background-color: $blue;
        }
    }

    &--border#{&}--white{
        border-color: #fff;
        fill: #fff;
        color: #fff;
        background-color: transparent;

        &:hover{
            border-color: #fff;
            background-color: #fff;
            color: $blue;
            fill: $blue;
        }
    }

    &--yellow{
        background-color: $yellow;
        color: $blue-dark;
        fill: $blue-dark;
        
        &:hover{
            opacity: 0.7;
            background-color: $yellow;
            color: $blue-dark;
            fill: $blue-dark;
        }
    }

    &--icon{
        padding: 0;
        width: 54px;
    }

    // input{
    //     display: none;
    // }

}