@import "@styles/vars.scss";

.course-intro {
    padding: 30px 0 80px;

    @media only screen and (max-width: $bp-xl){
        padding-bottom: 60px;
    }

    &__container{
        max-width: 1430px;
    }

    &__inner {
    }

    &__card {
    }

    &__stats {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        margin: 32px -20px -20px;

        @media only screen and (max-width: $bp-lg){
            margin: 48px -10px -20px;
        }

        @media only screen and (max-width: $bp-md){
            margin: 64px 0 -20px;
        }
    }

    &__stat-item {
        display: flex;
        align-items: center;
        margin: 0 20px 20px;

        @media only screen and (max-width: $bp-lg){
            width: calc(50% - 20px);
            margin: 0 10px 20px;
        }

        @media only screen and (max-width: $bp-md){
            justify-content: center;
            width: 100%;
            margin: 0 0 20px;
        }
    }

    &__stat-number {
        font-family: 'Inter';
        font-weight: 600;
        font-size: 80px;
        line-height: 120%;
        margin-right: 20px;
        color: $blue;

        @media only screen and (max-width: $bp-lg){
            font-size: 70px;
        }

        @media only screen and (max-width: $bp-md){
            min-width: 125px;
            text-align: right;
        }
    }

    &__stat-text {
        font-family: 'Inter';
        color: #606466;
        max-width: 220px;

        span{
            font-weight: 700;
        }
    }
}