@import "@styles/vars.scss";

.section-intro {
    background-color: #fff;
    overflow: hidden;
    
    @media only screen and (max-width: $bp-lg){
        padding-bottom: 35px;
    }

    @media only screen and (max-width: $bp-md){
        padding-bottom: 26px;
    }

    &__inner {
        position: relative;
    }

    &__elements {
    }

    &__slider{ 
        position: relative;
        z-index: 1;

        .slider {

            &__inner {
                overflow: visible;
            }
        
            &__wrapper {
            }
        
            &__slide {
                // opacity: 0 !important;
                // transition: opacity 0.5s ease;

                // &.swiper-slide-active{
                //     opacity: 1 !important;
                //     transition: opacity 0.75s ease 0.5s;
                // }
            }
        
            &__navigation {
                display: flex;
                align-items: center;
                position: absolute;
                bottom: 70px;
                left: 0;

                @media only screen and (max-width: $bp-xxl){
                    bottom: 54px;
                }
                
                @media only screen and (max-width: $bp-lg){
                    display: none;
                }
                
                @media only screen and (max-width: $bp-md){
                    
                }
            }
        
            &__button {
                margin: 0;
                box-sizing: border-box;
                position: static;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                display: flex;
                align-items: center;
                justify-content: center;
                width: 45px;
                height: 45px;
                border-radius: 50%;
                flex-shrink: 0;
                border: 2px solid $blue;
                fill: $blue;
                background-color: transparent;
                margin-left: 32px;
    
                &::after, 
                &::before{
                    content: none;
                }

                @media only screen and (max-width: $bp-xxl){
                    margin-left: 24px;
                    width: 34px;
                    height: 34px;
                }
                
                @media only screen and (max-width: $bp-lg){
                    
                }
                
                @media only screen and (max-width: $bp-md){
                    
                }

                &:first-of-type{
                    margin-left: 0;
                }
            }
    
            &__button-icon{
                flex-shrink: 0;
                width: 32px;
                height: 32px;

                @media only screen and (max-width: $bp-xxl){
                    width: 24px;
                    height: 24px;
                }
                
                @media only screen and (max-width: $bp-lg){
                    
                }
                
                @media only screen and (max-width: $bp-md){
                    
                }
            }

            &__pagination {
                position: static;
                width: auto;
                display: none;

                @media only screen and (max-width: $bp-lg){
                    width: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    margin-top: 20px;
                }

                @media only screen and (max-width: $bp-md){
                    margin-top: 35px;
                }
            }
            
            &__pagination-item{
                display: block;
                width: 12px;
                height: 12px;
                margin: 0 4px;
                border-radius: 50%;
                background-color: $gray;
                transition: background-color 0.35s ease;

                @media only screen and (max-width: $bp-lg){
                    width: 7px;
                    height: 7px;
                    margin: 0 2px;
                }
    
                &.swiper-pagination-bullet-active{
                    background-color: $blue;
                }
            }
        }

        .slider__slide.swiper-slide-active {
            .intro-item{
                &__label{
                    transform: translateX(0);
                    opacity: 1;
                    transition-duration: 0.9s;
                    transition-delay: 0.75s;
                }

                &__content{
                    transform: translateY(0);
                    opacity: 1;
                    transition-duration: 1s;
                    transition-delay: 0.5s;
                }

                &__img{
                    transform: translateX(0);
                    opacity: 1;
                    transition-duration: 1s;
                    transition-delay: 0.75s;
                }
            }
        }
    }

    &__item {
        padding-top: 115px;
        min-height: 852px;

        @media only screen and (max-width: $bp-xxl){
            min-height: 642px;
            padding-top: 80px;
        }
        
        @media only screen and (max-width: $bp-lg){
            padding-top: 505px;
        }
        
        @media only screen and (max-width: $bp-md){
            padding-top: 338px;
            min-height: 515px;
        }
    }

}

