.intro-item {
    box-sizing: border-box;
    position: relative;
    color: #000;
 

    &__content {
        position: relative;
        z-index: 2;
        width: 540px;
        transform: translateY(45px);
        opacity: 0;
        transition: transform 0.5s ease, opacity 0.5s ease;

        @media only screen and (max-width: $bp-xxl){
            width: 310px;
        }
    }

    &__labels {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-bottom: 16px;
        
        @media only screen and (max-width: $bp-xxl){
            margin-bottom: 12px;
        }

        @media only screen and (max-width: $bp-lg){
            display: none;
        }
    }

    &__label {
        position: relative;
        padding: 12px 20px;
        border-radius: 20px;
        font-weight: 700;
        color: #fff;
        font-size: 16px;
        line-height: 120%;
        margin-left: 42px;
        background-color: $blue;
        z-index: 1;

        @media only screen and (max-width: $bp-xxl){
            padding: 10px 16px;
            margin-left: 34px;
            font-size: 12px;
        }

        &::before{
            content:"";
            width: 14px;
            height: 14px;
            position: absolute;
            right: 100%;
            top: 50%;
            transform: translate(-14px,-50%);
            background-size: 100%;
            background-position: center;
            background-image: url("data:image/svg+xml,%3Csvg width='14' height='15' viewBox='0 0 14 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.81212 14.5V0.5H8.18788V14.5H5.81212ZM0 8.68788V6.31212H14V8.68788H0Z' fill='black'/%3E%3C/svg%3E%0A");

            @media only screen and (max-width: $bp-xxl){
                transform: translate(-10px, -50%);
            }
        }
        
        &:first-of-type{
            margin-left: 0;
            &::before{
                content: none;
            }
        }

        &:not(:first-of-type){
            z-index: 0;
            transform: translateX(-100%);
            opacity: 0;
        }

        &--green{
            background-color: $green;
        }
    }

    &__title {
        font-weight: 700;
        font-size: 60px;
        line-height: 105%;
        margin-bottom: 16px;

        @media only screen and (max-width: $bp-xxl){
            font-size: 45px;
            margin-bottom: 10px;
        }
        
        @media only screen and (max-width: $bp-lg){
            font-size: 38px;
            margin-bottom: 0;
        }
        
        @media only screen and (max-width: $bp-md){
            font-size: 30px;
        }
    }

    &__subititle {
        max-width: 385px;
        font-size: 16px;
        line-height: 150%;
        color: #888888;

        @media only screen and (max-width: $bp-xxl){
            font-size: 12px;
            max-width: 295px;
        }
        
        @media only screen and (max-width: $bp-lg){
            display: none;
        }
        
        @media only screen and (max-width: $bp-md){
            
        }
    }

    &__button {
        margin-top: 35px;

        @media only screen and (max-width: $bp-xxl){
            margin-top: 30px;
        }
        
        @media only screen and (max-width: $bp-lg){
            height: auto;
            width: auto;
            padding: 0;
            border: none;
            margin-top: 38px;
            font-size: 19px;

            &:hover{
                background-color: transparent;
            }
        }
        
        @media only screen and (max-width: $bp-md){
            margin-top: 32px;
            font-size: 16px;
        }
    }

    &__img {
        position: absolute;
        top: 38px;
        left: 426px;
        height: calc(100% - 38px);
        transform: translateX(150px);
        opacity: 0;
        transition: transform 0.5s ease, opacity 0.5s ease;

        @media only screen and (min-width: $bp-xl + 1) {
            width: 840px;
            height: 813px;
        }

        @media only screen and (max-width: $bp-xxl){
            left: 310px;
            top: 20px;
            height: calc(100% - 20px);
        }

        @media only screen and (max-width: $bp-lg){
            top: 50px;
            height: 522px;
            left: -54px;
        }

        @media only screen and (max-width: $bp-md){
            top: 5px;
            height: 436px;
            left: 50%;
            transform: translateX(-50%);
        }

        img{
            object-fit: contain;
            height: 100%;
        }
    }
}
