@import '@styles/vars.scss';

.course-result {
    margin-top: 110px;

    @media only screen and (max-width: $bp-xl) {
        margin-top: 80px;
    }

    @media only screen and (max-width: $bp-lg) {
        margin-top: 55px;
    }
    
    &__title {
        margin-bottom: 40px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 30px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 20px;
        }
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin: 0 -30px -60px;

        @media only screen and (max-width: $bp-xl) {
            margin-bottom: -50px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-right: 0;
            margin-left: 0;
        }
    }

    &__item {
        width: 50%;
        box-sizing: border-box;
        display: flex;
        padding: 0 30px;
        margin-bottom: 60px;

        @media only screen and (max-width: $bp-xl) {
            margin-bottom: 50px;
        }

        @media only screen and (max-width: $bp-lg) {
            align-items: center;
            width: 100%;
            padding: 0;
        }

    }

    &__item-img {
        flex-shrink: 0;
        width: 100px;
        height: 100px;
        margin-right: 24px;
        object-fit: contain;

        @media only screen and (max-width: $bp-lg) {
            width: 80px;
            height: 80px;
            margin-right: 16px;
        }
    }

    &__info {
    }

    &__item-title {
        margin-bottom: 16px;
    }

    &__item-text {
        color: $gray-dark;
    }
}