@import '@styles/vars.scss';

.section-features {
    overflow: hidden;
    padding: 100px 0;

    @media only screen and (max-width: $bp-xl){
        padding: 80px 0;
    }

    @media only screen and (max-width: $bp-lg){
        padding: 60px 0;
    }

    &__inner {
        display: flex;
        justify-content: space-between;
        background-color: #fff;
        padding: 48px 66px;
        border-radius: 36px;

        @media only screen and (max-width: $bp-xl) {
            padding: 32px 48px;
        }

        @media only screen and (max-width: $bp-lg) {
            padding: 24px 32px;
            flex-direction: column;
        }

        @media only screen and (max-width: $bp-md) {
            padding: 22px 24px;
        }
    }

    &__main {
        width: calc(50% - 40px);

        @media only screen and (max-width: $bp-xl) {
            width: calc(50% - 20px);
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            margin-bottom: 35px;
        }
    }

    &__title {
        margin-bottom: 32px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 20px;
        }
    }

    &__subtitle {
        color: #666;
        line-height: 150%;
        max-width: 375px;

        @media only screen and (max-width: $bp-lg) {
            max-width: none;
        }
    }

    &__logo {
        margin-top: 88px;
        width: 226px;
        height: 29px;
        object-fit: contain;

        @media only screen and (max-width: $bp-xl) {
            margin-top: 60px;
        }

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }
    }

    &__list {
        width: calc(50% - 40px);

        @media only screen and (max-width: $bp-xl) {
            width: calc(50% - 20px);
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
        }
    }

    &__item {
        display: flex;
        align-items: center;
        margin-bottom: 30px;

        &:last-of-type{
            margin-bottom: 0;
        }
    }

    &__item-icon {
        width: 110px;
        height: 110px;
        margin-right: 12px;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-xl) {
            width: 90px;
            height: 90px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 72px;
            height: 72px;
            margin-right: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 55px;
            height: 55px;
        }
    }

    &__item-content {
        position: relative;
        flex-grow: 1;
    }

    &__item-title {
        @media only screen and (max-width: $bp-lg) {
            font-size: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
        }
    }

    &__item-label {
        font-family: 'Inter';
        background-color: $green;
        padding: 7px 16px;
        border-radius: 20px;
        z-index: 1;
        position: absolute;
        top: 100%;
        transform: translateY(-26px) rotate(-3deg);
        left: 0;
        font-weight: 700;
        color: #fff;

        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
            padding: 3px 12px;
            transform: translateY(-16px) rotate(-3deg);
        }
    }

    &__item-label + &__item-title{
        padding-bottom: 26px;

        @media only screen and (max-width: $bp-lg) {
            padding-bottom: 16px;
        }
    }
}