@import "@styles/vars.scss";

.section-specialist {
    padding: 100px 0 80px;
    
    @media only screen and (max-width: $bp-xl){
        padding-top: 80px;
    }

    @media only screen and (max-width: $bp-lg){
        padding: 40px 0 60px;
    }

    &__header {
        margin-bottom: 35px;
    }

    &__link {
        display: none;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 12px;
            display: inline-flex;
        }
    }

    &__slider {
        
        .slider {

            &__inner {
                overflow: hidden;
                border-radius: 20px;
                margin: 0 -35px;
                padding: 0 35px;
            }

            &__navigation {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                margin: 0 -35px;
                min-width: 100%;
                justify-content: space-between;
            }

            &__button {
                width: 70px;
                height: 70px;
                fill: #fff;
                background-color: $blue;
                padding: 6px;
                border: none;

                @media only screen and (max-width: $bp-lg) {
                    display: none;
                }

                &:hover{
                    background-color: $blue-dark;
                }

                &--prev {
                    justify-content: flex-start;
                }

                &--next {
                    justify-content: flex-end;
                }
            }

            &__button-icon {
                width: 32px;
                height: 32px;
            }

            &__pagination {
                display: none;

                @media only screen and (max-width: $bp-lg) {
                    display: flex;
                }

                @media only screen and (max-width: $bp-md) {
                    margin-top: 20px;
                }
            }
        }

    }

    &__features {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin: 45px -5px -50px;
    }

    &__feature {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        padding: 0 5px;
        width: 33.33%;
        margin-bottom: 50px;

        @media only screen and (max-width: $bp-xl) {
            width: 50%;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
        }
    }

    &__feature-icon {
        flex-shrink: 0;
        width: 110px;
        height: 89px;
        margin-right: 20px;

        @media only screen and (max-width: $bp-lg) {
            width: 98px;
            height: 79px;
        }
    }

    &__feature-title {

        @media only screen and (max-width: $bp-lg) {
            font-size: 18px;
        }
    }

    &__tabs {
        display: flex;
        flex-wrap: wrap;
        margin: 45px -20px -10px;

        @media only screen and (max-width: $bp-xl) {
            margin: 35px -15px -10px;
        }

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }
    }

    &__tab-link {
        font-family: 'Inter';
        margin: 0 20px 10px;
        text-align: left;
        width: calc(100% / 6 - 40px);
        

        @media only screen and (max-width: $bp-xxl) {
            width: calc(100% / 5 - 40px);
        }
        
        @media only screen and (max-width: $bp-xl) {
            width: calc(100% / 4 - 30px);
            margin: 0 15px 10px;
        }

        &.is-active{
            cursor: default;
            pointer-events: none;
            color: $black;
        }
    }
}