@import '@styles/vars.scss';

.course-info {
    padding: 80px 0 50px;

    @media only screen and (max-width: $bp-lg){
        padding-top: 60px;
    }

    &__inner {
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        background-color: #fff;
        border-radius: 20px;
        padding: 50px 60px 55px;
        margin-bottom: 50px;

        @media only screen and (max-width: $bp-xl) {
            padding: 50px 32px 32px;
        }

        @media only screen and (max-width: $bp-lg) {
            flex-direction: column;
            justify-content: flex-start;
        }

        @media only screen and (max-width: $bp-md) {
            padding-right: 16px;
            padding-left: 16px;
        }
    }

    &__main {
        width: 430px;
        margin-right: 60px;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-xl) {
            width: 350px;
            margin-right: 40px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            margin-right: 0;
        }
    }

    &__title {
        font-size: 35px;
        margin-bottom: 40px;

        @media only screen and (max-width: $bp-xl) {
            font-size: 30px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 25px;
            margin-bottom: 32px;
        }

        span{
            color: $blue;
        }
    }

    &__radio-tumbler {
        margin-bottom: 50px;
        background-color: #DFF2FD;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 35px;
        }
    }

    &__description {
        line-height: 150%;
        margin-bottom: 25px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 35px;
        }
    }

    &__time-block {
        display: flex;
        align-items: center;
        margin-bottom: -16px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 40px;
        }
    }

    &__time-icon{
        width: 50px;
        height: 50px;
        margin-right: 14px;
        flex-shrink: 0;
        fill: #cccccc;
    }

    &__time {
        display: flex;
        align-items: center;
        line-height: 120%;
        flex-grow: 1;

        @media only screen and (max-width: $bp-xl) {
            flex-wrap: wrap;
        }

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
            align-items: flex-start;
        }

        span{
            margin-right: auto;

            br{
                @media only screen and (max-width: $bp-xl) {
                    display: none;
                }
            }
        }
    }   

    &__time-main {
        font-family: 'Inter';
    }

    &__content {
        flex: 1 1 auto;
    }

    &__item {
        display: flex;
        flex-direction: column;
        display: none;

        &.is-open{
            display: flex;
            animation: fadeInTop 0.5s ease;
        }
    }

    &__item-subtitle {
        margin-bottom: 6px;
    }

    &__item-title {
        margin-bottom: 30px;

        @media only screen and (max-width: $bp-xl) {
            font-size: 30px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 25px;
            margin-bottom: 64px;
        }
    }

    &__year {
        display: flex;
        align-items: center;
        width: 100%;
        justify-content: space-between;
        padding-bottom: 32px;

        span{
            display: inline-flex;
            align-items: center;
            flex-shrink: 0;
            margin: 0 16px;
        }

        &::after,
        &::before {
            content: "";
            display: flex;
            height: 1px;
            background-color: rgba(0, 0, 0, 0.2);
            flex-grow: 1;
        }
    }

    &__list {
        display: block;
        column-count: 2;
        margin: 0 0 -32px;
        padding: 0;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: -16px;
        }

        @media only screen and (max-width: $bp-md) {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }

    &__list-item {
        display: inline-flex;
        align-items: center;
        width: 205px;
        box-sizing: border-box;
        padding-left: 25px;
        margin-bottom: 24px;

        @media only screen and (max-width: $bp-xl) {
            width: 180px;
            padding-left: 20px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 16px;
            padding-left: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            padding-left: 0;
            text-align: center;
            justify-content: center;
        }

        p{
            font-family: 'Inter';
            font-weight: 300;
            font-size: 30px;
            line-height: 120%;

            @media only screen and (max-width: $bp-xl) {
                font-size: 25px;
            }

            @media only screen and (max-width: $bp-md) {
                font-size: 20px;
            }

            span{
                font-size: 20px;
            }
        }
    }

    &__item.is-active &__accordion-trigger .icon{
        transform: rotate(-180deg);
    }

    &__accordion-trigger{
        display: none;
        justify-content: space-between;
        width: 100%;

        .icon{
            transition: transform 0.35s ease
        }

        @media only screen and (max-width: $bp-lg) {
            display: flex;
        }
    }

    &__accordion-body{
        @media only screen and (max-width: $bp-lg) {
            height: 0;
            overflow: hidden;
            transition: height 0.5s ease;
        }
    }

    &__accordion-content{
        @media only screen and (max-width: $bp-lg) {
            padding-top: 32px;
        }
    }
}
