@import "@styles/vars.scss";

.section-404 {
    padding-top: 160px;
    padding-bottom: 90px;

    @media only screen and (max-width: $bp-xl){
        padding-top: 120px;
    }

    @media only screen and (max-width: $bp-lg){
        padding-top: 80px;
        padding-bottom: 60px;
    }

    @media only screen and (max-width: $bp-md){
        padding-top: 60px;
        padding-bottom: 40px;
    }

    &__container{
        max-width: 1630px;
    }

    &__inner {
        display: flex;
        justify-content: center;

        @media only screen and (max-width: $bp-xl){
            flex-direction: column;
            align-items: center;
        }
    }

    &__img {
        display: block;
        flex-shrink: 0;
        width: 765px;
        height: auto;
        object-fit: contain;
        margin-right: 80px;

        @media only screen and (max-width: $bp-xxl){
            width: 570px;
            margin-right: 60px;
        }

        @media only screen and (max-width: $bp-xl){
            margin-right: 0;
        }

        @media only screen and (max-width: $bp-lg){
            width: calc(100% - 40px);
            max-width: 540px;
        }

        @media only screen and (max-width: $bp-md){
            width: calc(100% - 20px);
        }
    }

    &__main {
        width: 100%;
        max-width: 460px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        @media only screen and (max-width: $bp-xl){
            text-align: center;
            align-items: center;
            margin-top: -50px;
        }
    }

    &__title {
        font-weight: 600;
        margin-bottom: 30px;

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 20px;
        }
    }

    &__text {
        line-height: 150%;
        margin-bottom: 94px;

        @media only screen and (max-width: $bp-xxl){
            margin-bottom: 75px;
        }

        @media only screen and (max-width: $bp-xl){
            margin-bottom: 45px;
        }

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 32px;
            font-size: 16px;
        }

        @media only screen and (max-width: $bp-md){
            margin-bottom: 24px;
        }
    }

    &__button {
    }
}