@import "@styles/vars.scss";

.card-article {
    position: relative;
    box-sizing: border-box;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 396px;
    border-radius: 20px;
    background-color: #fff;
    text-decoration: none;
    color: #000;
    box-shadow: 0px 0px 44px #E1F4FF;
    transition: box-shadow 0.75s ease;

    @media only screen and (max-width: $bp-lg) {
        border-radius: 14px;
    }

    @media only screen and (max-width: $bp-md) {
        min-height: 364px;
    }

    &:hover{
        box-shadow: 0px 0px 44px #D3EEFF;
    }

    &:hover &__img{
        transform: scale(103%);
    }

    &__img-wrapper {
        height: 232px;
        width: 100%;
        overflow: hidden;
    }

    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        will-change: transform;
        transition: transform 0.75s ease;
    }

    &__content {
        position: relative;
        z-index: 3;
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        padding: 12px 15px 10px;

        @media only screen and (max-width: $bp-lg) {
            padding: 15px;
        }
    }

    &__subtitle{
        margin-bottom: 4px;
        color: #B3B3B3;
    }

    &__title {
        font-size: 16px;
        line-height: 120%;
        -webkit-line-clamp: 4;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        margin-bottom: 20px;

        @media only screen and (max-width: $bp-lg) {
            -webkit-line-clamp: 3;
        }

        @media only screen and (max-width: $bp-md) {
            -webkit-line-clamp: 2;
            font-size: 18px;
        }
    }

    &__info {
        margin-top: auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__name {
        font-family: 'Inter';
        font-size: 12px;
        line-height: 120%;
        display: flex;
        flex-direction: column;

        @media only screen and (max-width: $bp-lg) {
            flex-direction: row;
        }

        span{
            font-weight: 600;
            color: #B3B3B3;
            margin-bottom: 5px;

            @media only screen and (max-width: $bp-lg) {
                margin-bottom: 0;
                margin-right: 6px;
            }
        }
    }

    &__author-wrapper {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        overflow: hidden;
        margin-left: 10px;

        @media only screen and (max-width: $bp-lg) {
            width: 40px;
            height: 40px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 34px;
            height: 35px;
        }
    }

    &__author-img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &--lg{
        @media only screen and (min-width: $bp-md + 1) {
            color: #fff;

            &::after{
                content: "";
                display: block;
                width: 100%;
                height: 100%;
                position: absolute;
                z-index: 1;
                background-position: bottom;
                background: linear-gradient(360deg, rgba(21, 127, 193, 0.7) 8.02%, rgba(58, 179, 254, 0) 65.78%);
                transform-origin: bottom;
                will-change: transform;
                transition: transform 0.5s ease;
            }

            &:hover::after{
                transform: scaleY(160%);
            }
        }

        @media only screen and (min-width: $bp-lg + 1){
            min-height: 430px;
        }
    }

    &--lg &__img-wrapper{
        @media only screen and (min-width: $bp-md + 1){
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }

    &--lg &__content{
        @media only screen and (min-width: $bp-md + 1){
            padding: 15px 24px;
            flex-direction: row;
            align-items: center;
            margin-top: auto;
            flex-grow: 0;
        }

        @media only screen and (min-width: $bp-lg + 1){
            padding: 32px 35px 32px 52px;
        }
    }

    &--lg &__subtitle{
        @media only screen and (min-width: $bp-md + 1){
            display: none;
        }
    }

    &--lg &__title{
        @media only screen and (min-width: $bp-md + 1){
            font-size: 25px;
            margin-right: 15px;
            max-width: 50%;
            margin-bottom: 0;
        }
    }

    &--lg &__info{
        @media only screen and (min-width: $bp-md + 1){
            justify-content: flex-end;
            margin-left: auto;
        }
    }
    
    &--lg &__name{
        @media only screen and (min-width: $bp-md + 1){
            text-align: right;
            span{
                color: #fff;
                opacity: 0.5;
            }
            
            .link{
                color: #fff;
            }
        }
    }

    &--teacher{
        @media only screen and (min-width: $bp-md + 1) {
            color: #fff;

            &::after{
                content: "";
                display: block;
                width: 100%;
                height: 100%;
                position: absolute;
                z-index: 1;
                background-position: bottom;
                background: linear-gradient(360deg, rgba(21, 127, 193, 0.7) 8.02%, rgba(58, 179, 254, 0) 65.78%);
                transform-origin: bottom;
                will-change: transform;
                transition: transform 0.5s ease;
            }

            &:hover::after{
                transform: scaleY(160%);
            }
        }

        @media only screen and (min-width: $bp-lg + 1){
            min-height: 426px;
        }
    }

    &--teacher &__img-wrapper{
        @media only screen and (min-width: $bp-md + 1){
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }

    &--teacher &__content{
        @media only screen and (min-width: $bp-md + 1){
            padding: 15px 24px;
            flex-direction: column;
            margin-top: auto;
            flex-grow: 0;
        }
        
        @media only screen and (min-width: $bp-lg + 1){
            padding: 32px 35px 32px 52px;
        }
    }

    &--teacher &__subtitle{
        color: rgba(255, 255, 255, 0.8);

        @media only screen and (min-width: $bp-md + 1){
            font-size: 14px;
        }

        @media only screen and (min-width: $bp-lg + 1){
            font-size: 16px;
        }
    }

    &--teacher &__title{
        @media only screen and (min-width: $bp-md + 1){
            font-size: 25px;
            margin-bottom: 0;
        }
    }

    &--teacher &__info{
        @media only screen and (min-width: $bp-md + 1){
            display: none;
        }
    }
    
    &--teacher &__name{
        @media only screen and (min-width: $bp-md + 1){
            text-align: right;
            span{
                color: #fff;
                opacity: 0.5;
            }
            
            .link{
                color: #fff;
            }
        }
    }
}

.card-socials {
    position: relative;
    box-sizing: border-box;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 396px;
    border-radius: 20px;
    padding: 40px;
    background-color: $blue;
    color: #fff;
    box-shadow: 0px 0px 44px #E1F4FF;

    &--white{
        background-color: #fff;
        color: $blue;
    }

    @media only screen and (max-width: $bp-xl) {
        padding: 24px;
    }

    @media only screen and (max-width: $bp-md) {
        min-height: 158px;
        padding: 22px 20px;
        align-items: flex-start;
    }

    &__icon{
        width: 24px;
        height: 24px;
        position: absolute;
        top: 20px;
        right: 20px;

        @media only screen and (max-width: $bp-md) {
            width: 32px;
            height: 32px;
            top: 28px;
        }
    }

    &__title {
        text-align: center;

        @media only screen and (max-width: $bp-lg) {
            font-size: 20px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-right: 45px;
            text-align: left;
            font-size: 18px;
        }
    }

    &__content {
        margin-top: 25px;

        @media only screen and (max-width: $bp-md) {
            margin-top: 40px;
        }
    }
    &--white .socials__item{
        fill: $blue;
    }
}
