@import "@styles/vars.scss";

.section-articles {
    padding: 60px 0;

    @media only screen and (max-width: $bp-lg){
        padding: 45px 0;
    }
    
    &__inner {

    }

    &__header {
        margin-bottom: 40px;
        padding-right: 300px;

        @media only screen and (max-width: $bp-xl) {
            padding-right: 0;
        }
    }

    &__title {
        margin-bottom: 20px;

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 32px;
        }
    }

    &__tags {
    }

    &__body {
        &::after{
            content:"";
            display: block;
            clear: both;
        }

        @media only screen and (max-width: $bp-xl){
            display: flex;
            flex-direction: column;
        }
    }

    &__main {
        display: flex;
        flex-wrap: wrap;
        float: left;
        width: calc(100% - 280px);

        margin: 0 -10px 15px;

        @media only screen and (max-width: $bp-xl){
            margin-bottom: 8px;
            width: auto;
        }

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 15px;
        }

        @media only screen and (max-width: $bp-md){
            margin-right: 0;
            margin-left: 0;
        }

        .card-article{
            margin: 0 10px 45px;
            width: calc(33.33% - 20px);

            @media only screen and (max-width: $bp-xl){
                margin-bottom: 32px;
            }

            @media only screen and (max-width: $bp-lg){
                margin-bottom: 25px;
                width: calc(50% - 20px);
            }

            @media only screen and (max-width: $bp-md){
                width: 100%;
                margin-right: 0;
                margin-left: 0;
            }

            &--lg{
                width: calc(100% - 20px);
                
                @media only screen and (max-width: $bp-md){
                    width: 100%;
                }
            }
        }

        &:last-of-type{
            margin-bottom: -45px;

            @media only screen and (max-width: $bp-xl){
                margin-bottom: -32px;
            }

            @media only screen and (max-width: $bp-lg){
                margin-bottom: -25px;
            }
        }
    }

    &__aside {
        width: 270px;
        margin-left: auto;
        position: sticky;
        top: 80px;
        float: right;

        @media only screen and (max-width: $bp-xl){
            position: static;
            width: 100%;
            margin: 15px 0 40px;
        }
    }

    &__pagination {
        padding-right: 300px;

        @media only screen and (max-width: $bp-xl){
            padding-right: 0;
        }
    }
}