@import "@styles/vars.scss";

.card-specialist {
    box-sizing: border-box;
    min-height: 436px;
    border-radius: 20px;

    &__inner {
        display: flex;

        @media only screen and (max-width: $bp-lg) {
            flex-direction: column;
        }
    }

    &__img-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        width: 400px;
        min-height: 100%;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-xl) {
            width: 350px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            min-height: 0;
            height: auto;
            aspect-ratio: 2.2;
        }

        @media only screen and (max-width: $bp-md) {
            min-height: 148px;
        }
    }

    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 20px 0 0 20px;
        object-position: center;

        @media only screen and (max-width: $bp-lg) {
            border-radius: 20px 20px 0 0;
        }
    }

    &__video-link{
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        width: 96px;
        height: 96px;
        bottom: 40px;
        right: 0;
        transform: translateX(50%);

        @media only screen and (max-width: $bp-lg) {
            width: 75px;
            height: 75px;
            bottom: 0;
            transform: translate(8px, 40%);
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__main{
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        background-color: #fff;
        border-radius: 0 20px 20px 0;
        padding: 25px 150px 25px 85px;

        @media only screen and (max-width: $bp-xxl) {
            padding: 25px 60px;
        }

        @media only screen and (max-width: $bp-lg) {
            padding: 20px 32px;
        }

        @media only screen and (max-width: $bp-md) {
            padding: 12px 16px 20px;
        }
    }

    &__title {
        margin-bottom: 5px;
        
        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 0;
        }
    }

    &__role {
        margin-bottom: 14px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 6px;
        }
    }

    &__text {
        line-height: 150%;
        color: #666;
        margin-bottom: 24px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 10px;
        }
    }

    &__regalia {
        display: flex;
        align-items: center;
        background: rgba(58, 179, 254, 0.1);
        border-radius: 10px;
        padding: 16px 32px;

        @media only screen and (max-width: $bp-lg) {
            padding: 12px 24px;
        }

        @media only screen and (max-width: $bp-md) {
            padding: 10px 16px;
        }
    }

    &__regialia-img {
        width: 64px;
        height: 64px;
        margin-right: 24px;
        flex-shrink: 0;
        
        @media only screen and (max-width: $bp-lg) {
            width: 68px;
            height: 68px;
            margin-right: 12px;
        }
    }

    &__regalia-text {
        color: #666;
    }

    &__link {
        margin-top: 20px;
        margin-left: auto;

        @media only screen and (max-width: $bp-lg) {
            margin-left: 0;
        }
    }
}