@import "@styles/vars.scss";

.teacher-courses{
    padding: 60px 0 60px;

    @media only screen and (max-width: $bp-lg){
        padding: 80px 0 40px;
    }

    &__title{
        margin-bottom: 40px;

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 30px;
        }
    }

    &__list{
        margin-bottom: -20px;

        @media only screen and (max-width: $bp-lg){
            margin-bottom: -25px;
        }
    }

    &__card{
        margin-bottom: 20px;

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 25px;
        }
    }
}