@import '@styles/vars.scss';

.about-description {
    padding: 150px 0 20px;
    
    @media only screen and (max-width: $bp-xl){
        padding-top: 110px;
    }
    
    @media only screen and (max-width: $bp-lg){
        padding: 70px 0 30px;
    }
    
    &__inner {
        display: flex;
        align-items: center;
    }

    &__main {
        margin-top: 50px;
        width: 450px;

        @media only screen and (max-width: $bp-xl){
            margin-top: 20px;
        }

        @media only screen and (max-width: $bp-lg){
            margin-top: 0;
            width: 100%;
        }
    }

    &__title {
        margin-bottom: 26px;

        @media only screen and (max-width: $bp-md){
            margin-bottom: 22px;
        }
    }

    &__text {
        color: #666;
        margin-bottom: 24px;

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 20px;
        }

        &:last-of-type{
            margin-bottom: 0;
        }
    }

    &__img {
        width: auto;
        object-fit: contain;
        height: 550px;
        margin-left: 80px;

        @media only screen and (max-width: $bp-xl){
            height: 460px;
            margin-left: 60px;
        }

        @media only screen and (max-width: $bp-lg){
            display: none;
        }
    }
}