@import '@styles/vars.scss';

.course-diplom {
    padding: 40px 0;

    @media only screen and (max-width: $bp-lg) {
        display: none;
    }

    &__inner {
        display: flex;
    }

    &__main {
        width: 220px;
        flex-shrink: 0;
        margin-right: 30px;
        
    }

    &__title {
        margin-bottom: 20px;
    }

    &__links {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-bottom: -16px;
    }

    &__link {
        margin: 0 8px 16px;

        &.is-active{
            color: $blue-dark;
            pointer-events: none;
        }
    }

    &__navigation {
        margin-top: 32px;

        .slider{
            
            &__navigation {
            }
        
            &__button {
                margin-right: 16px;

                &:last-of-type{
                    margin-right: 0;
                }
            }
        
            &__button--prev {
            }
        
            &__button-icon {
            }
        
            &__button--next {
            }
        }
    }

    &__slider {
        width: 100%;
        width: calc(100% - 250px);

        @media only screen and (max-width: $bp-xl) {
            width: 600px;
        }
    }

    &__img-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 425px;

        img{
            max-width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
}
