@import '@styles/vars.scss';

.course-free {
    padding: 60px 0 40px;
    text-decoration: none;

    @media only screen and (max-width: $bp-xl){
        padding-bottom: 60px;
    }

    @media only screen and (max-width: $bp-lg){
        padding-bottom: 80px;
    }
    
    &__inner {
        box-sizing: border-box;
        position: relative;
        border-radius: 20px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        height: 470px;
        padding: 80px 100px;
        color: #fff;
        background-color: $blue;

        @media only screen and (max-width: $bp-xl) {
            height: 440px;
            padding: 60px 75px;
        }
        
        @media only screen and (max-width: $bp-lg) {
            height: auto;
            align-items: center;
            padding: 24px 28px 32px;
        }
    }

    &__title {
        display: inline-flex;
        flex-direction: column;
        align-items: flex-start;
        max-width: 380px;
        margin-bottom: auto;

        @media only screen and (max-width: $bp-lg) {
            max-width: none;
            margin-bottom: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 35px;
        }
    }

    &__label {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        font-size: 35px;
        font-weight: 700;
        line-height: 120%;
        padding: 0 40px;
        height: 56px;
        border-radius: 30px;
        background-color: $green;
        transform: rotate(-4deg);

        @media only screen and (max-width: $bp-lg) {
            font-size: 30px;
            font-weight: 400;
            height: 46px;
            padding: 0 16px;
            margin-left: auto;
        }
    }

    &__img {
        display: block;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 0;
        height: 470px;
        aspect-ratio: 1;
        width: auto;

        @media only screen and (max-width: $bp-xl) {
            height: 400px;
        }

        @media only screen and (max-width: $bp-lg) {
            position: static;
            transform: none;
            margin: 0 auto;
            width: 100%;
            height: auto;
            max-width: 400px;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__button {
        margin-top: 38px;
    }
}