@import "@styles/vars.scss";

.section-questions {
    padding: 50px 0 100px;

    @media only screen and (max-width: $bp-xl) {
        padding-bottom: 80px;
    }

    @media only screen and (max-width: $bp-lg) {
        padding: 40px 0;
    }

    &__inner {
    }

    &__header {
        display: flex;
        align-items: center;
        margin-bottom: 40px;

        @media only screen and (max-width: $bp-lg) {
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: 50px;
        }
    }

    &__title{
        margin-right: 90px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 20px;
            margin-right: 0;
        }
    }

    &__button {
        height: 50px;
    }

    &__items {
    }

    &__resolution {
        margin-top: 85px;
    }
}