@import "@styles/vars.scss";

.input{
    box-sizing: border-box;
    background: none;
    border: none;
    outline: none;
    display: flex;
    align-items: center;
    width: 100%;
    color: $blue;
    padding: 0 8px 8px;
    font-weight: 400;
    font-family: "Inter";
    font-size: 14px;
    background-color: transparent;
    border-bottom: 3px solid $blue;
    transition: background-color 0.35s ease 0.1s, border-color 0.35s ease, color 0.35s ease;
    

    &::placeholder{
        color: inherit;
        opacity: 0.5;
    }

    &--white{
        color: #fff;
        border-color: #fff;
    }

    &--round{
		height: 60px;
		padding: 0 36px;
		border: 3px solid #EBF7FF;
		margin-bottom: 25px;
		font-size: 25px;
		line-height: 120%;
		font-weight: 700;
		color: #000;
		background-color: #EBF7FF;
		border-radius: 40px;
		outline: none;

		@media only screen and (max-width: $bp-lg){
			font-size: 20px;
			padding: 0 24px;
		}

		@media only screen and (max-width: $bp-md){
			font-size: 18px;
			padding: 0 18px;
		}
		
		&.is-focus{
			background-color: transparent;
		}

		&.is-filled{
			background-color: transparent;
			border-color: #ACDDFF;
		}

		&::placeholder{
			font-weight: 500;
			font-size: 18px;
			color: rgba(0, 0, 0, 0.5);

			@media only screen and (max-width: $bp-md){
				font-size: 16px;
			}
		}
    }

    &.is-error{
        border-color: #F6537A;
    }

	&.is-focus{
		border-color: $blue-light;
	}

}