@import "@styles/vars.scss";

.socials {
    &__media{
        display: flex;
        align-items: center;
    }

    &__list {
        display: flex;
        justify-content: center;
        align-items: center;

        @media only screen and (max-width: $bp-md) {
            justify-content: flex-start;
        }
    }

    &__item {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-shrink: 0;
        width: 38px;
        height: 38px;
        fill: #fff;
        transition: opacity 0.35s ease;

        @media only screen and (max-width: $bp-md) {
            width: 30px;
            height: 30px;
        }

        &:hover{
            opacity: 0.8;
        }
    }

    &__item-icon{
        width: 100%;
        height: 100%;
    }

    &__partner {
        display: flex;
        flex-direction: column;
        text-decoration: none;
    }

    &__partner-label {
        margin: 0 0 16px;
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: $gray-dark;
    }

    &__partner-logo {
        width: 272px;
        height: auto;

        img{
            width: 100%;
        }

        @media only screen and (max-width: $bp-md){
            width: 180px;
        }
    }
}
