@import "@styles/vars.scss";

.aside-section{
    position: relative;

    section .container>[class*="__inner"]{
        padding-right: 285px;

        @media only screen and (max-width: $bp-xxl) {
            padding-right: 275px;
        }

        @media only screen and (max-width: $bp-xl) {
            padding-right: 0;
        }
    }
}