@import "@styles/vars.scss";

.tags{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: -14px;

    &__item{
        cursor: pointer;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 20px;
        text-decoration: none;
        font-family: 'Raleway';
        font-weight: 500;
        font-size: 14px;
        line-height: 120%;
        color: $blue;
        margin: 0 40px 14px 0;
        transition: background-color 0.35s ease, color 0.35s ease;

        @media only screen and (max-width: $bp-lg){
            margin-right: 30px;
        }

        @media only screen and (max-width: $bp-md){
            margin-right: 20px;
        }

        &:hover{
            color: $blue-dark;
        }

        &.is-active{
            padding: 0 20px;
            height: 30px;
            pointer-events: none;
            background-color: $blue;
            color: #fff;
        }
    }   
}