@import '@styles/vars.scss';

.course-program {
    padding: 20px 0 50px;
    
    @media only screen and (max-width: $bp-lg){
        padding-bottom: 40px;
    }

    &__header {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-bottom: 38px;
    }

    &__title {
        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            margin-bottom: 12px;
        }
    }

    &__header-info {
    }

    &__time {
        display: none;
        align-items: center;
        fill: #A3A3A3;
        color: #000;

        @media only screen and (max-width: $bp-lg) {
            display: inline-flex;
        }

        .icon{
            width: 28px;
            height: 28px;
            flex-shrink: 0;
            margin-right: 8px;

        }
    }

    &__label {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        height: 36px;
        padding: 0 16px;
        margin-left: 20px;
        background-color: $blue;
        color: #fff;
        border-radius: 20px;

        @media only screen and (max-width: $bp-lg) {
            margin-left: 10px;
        }
    }

    &__inner {
    
    }

    &__item{
        margin-bottom: 20px;

        &.is-active{
            margin-bottom: 32px;
        }
        
        &:last-of-type{
            margin-bottom: 0;
        }
    }
}