@import '@styles/vars.scss';

.radio-tumbler {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px;
    height: 65px;
    border-radius: 50px;
    background-color: #fff;
    width: 100%;

    &__item {
        cursor: pointer;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        flex: 1 1 50%;
        color: rgba(0, 0, 0, 0.5);
        height: 100%;
        border-radius: 50px;
        transition: color 0.35s ease, background-color 0.35s ease;

        input{
            display: none;
        }

        @media only screen and (max-width: $bp-lg) {
            height: 45px;
        }

        span{
            @media only screen and (max-width: $bp-lg) {
                font-size: 18px;
            }

            @media only screen and (max-width: $bp-md) {
                font-size: 16px;
            }
        }

        &:hover{
            color: $blue;
        }

        &.is-active{
            pointer-events: none;
            background-color: $blue;
            color: #fff;
        }
    }
}