@import '@styles/vars.scss';
.course-event {
    padding: 30px 0;

    @media only screen and (max-width: $bp-lg) {
        display: none;
    }

    &__inner {
        padding: 0 55px;
    }

    &__card + &__card{
        margin-top: 45px;

        @media only screen and (max-width: $bp-lg){
            margin-top: 32px;
        }
    }
}