@import '@styles/vars.scss';

.section-teachers{
    overflow: hidden;
    position: relative;
    padding: 60px 0 100px;

    @media only screen and (max-width: $bp-xl){
        padding-bottom: 80px;
    }

    @media only screen and (max-width: $bp-lg) {
        padding-top: 45px;
        padding-bottom: 60px;
    }

    &__title{
        margin-bottom: 55px;

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 45px;
        }

        @media only screen and (max-width: $bp-md){
            margin-bottom: 35px;
            font-size: 36px;
        }
    }

    &__content{
        margin: 0 auto;
        max-width: 930px;
        display: block;
        width: 100%;
        box-sizing: border-box;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;

        @media only screen and (max-width: $bp-xl) {
            max-width: none;
            justify-content: space-between;
            margin-bottom: -80px;
        }

        @media only screen and (max-width: $bp-lg){
            margin-bottom: -60px;
        }
    }

    &__item{
        width: 100%;
    }

    &__item-wrapper{
        box-sizing: border-box;
        height: auto;
        margin-right: 30px;
        margin-left: 30px;
        width: 250px;
        margin-top: 100px;
        min-height: 408px;

        @media only screen and (max-width: $bp-xl) {
            margin-top: 0;
            margin-right: 20px;
            margin-left: 20px;
            width: calc(33.33% - 40px);
            margin-bottom: 80px;
            min-height: auto;
            order: 3;
        }

        @media only screen and (max-width: $bp-lg){
            width: calc(50% - 40px);
            margin-bottom: 60px;
            min-height: auto;
        }

        @media only screen and (max-width: $bp-md){
            width: calc(50% - 16px);
            margin-right: 8px;
            margin-left: 8px;
        }

        &--other{
            @media only screen and (max-width: $bp-xl){
                width: calc(66.66% - 40px);
                width: 100%;
                display: flex !important;
                justify-content: center;
                margin-right: 0;
                margin-left: 0;
            }
        }

        @media only screen and (min-width: $bp-xl + 1){
            margin-top: 100px;
                        
            &:nth-of-type(1),
            &:nth-of-type(3){
                margin-top: 225px;
            }

            &:nth-of-type(3n + 2):not(:last-of-type){
                margin-top: -145px;
            }

            &:nth-of-type(2){
                margin-top: 0 !important;
            }
        }

        @media only screen and (min-width: $bp-lg + 1) and (max-width: $bp-xl){
            display: none;

            &:nth-of-type(2),
            &:nth-of-type(3),
            &:nth-of-type(4),
            &:nth-of-type(5),
            &:nth-of-type(6),
            &:nth-of-type(7), {
                display: block;
                order: 1;
            }

            &:nth-of-type(8),
            &:nth-of-type(9),
            &:nth-of-type(10),
            &:nth-of-type(11),
            &:nth-of-type(13),
            &:nth-of-type(14) {
                display: block;
                order: 2;
            }

            &:nth-of-type(15),
            &:nth-of-type(16),
            &:nth-of-type(17),
            &:nth-of-type(18),
            &:nth-of-type(19),
            &:nth-of-type(20) {
                display: block;
                order: 3;
            }
           
            &:nth-of-type(1){
                display: block;
                order: 0 !important;
            }

            &:nth-of-type(12){
                display: block;
                order: 1 !important;
            }
           
            &:nth-of-type(23){
                display: block;
                order: 2 !important;
            }
        }

        @media only screen and (max-width: $bp-lg){
            display: none;

            &:nth-of-type(2),
            &:nth-of-type(3),
            &:nth-of-type(4),
            &:nth-of-type(5) {
                display: block;
                order: 1;
            }

            &:nth-of-type(8),
            &:nth-of-type(9),
            &:nth-of-type(10),
            &:nth-of-type(11) {
                display: block;
                order: 2;
            }

            &:nth-of-type(15),
            &:nth-of-type(16),
            &:nth-of-type(17),
            &:nth-of-type(18) {
                display: block;
                order: 3;
            }
           
            &:nth-of-type(1){
                display: block;
                order: 0 !important;
            }

            &:nth-of-type(12){
                display: block;
                order: 1 !important;
            }
           
            &:nth-of-type(23){
                display: block;
                order: 2 !important;
            }
        }
    }

    &__title-wrapper{
        width: 290px;
        flex-shrink: 0;
    }

    &__item-title{
        color: $blue;

        strong{
            font-size: 130px;
            line-height: 95%;
        }

        span{
            display: inline-flex;
            font-size: 65px;
            line-height: 100%;
        }
    }

    &__item-subtitle{
        margin-top: 14px;
    }

    &__inner{
        position: relative;
    }

    &__button-more{
        order: 4;
        margin: 40px auto 0;
        display: none;

        @media only screen and (max-width: $bp-xl){
            display: inline-flex;
        }
    }

    &__slider{
        width: 100%;
        margin-top: -154px;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 0;
        }

        @media only screen and (min-width: $bp-lg + 1) {
            .slider{
                &__wrapper{
                    display: block;
                    width: 100%;
                    box-sizing: border-box;
                    display: flex;
                    justify-content: center;
                    flex-wrap: wrap;
    
                    &::after{
                        content: "";
                        display: flex;
                        margin: 80px 30px 0;
                        width: 250px;
                        order: 1;
    
                        @media only screen and (max-width: $bp-xl) {
                            margin: 0 20px;
                            display: none;
                        }
                    }
                }
    
                &__slide{
                    box-sizing: border-box;
                    height: auto;
                    margin-right: 30px;
                    margin-left: 30px;
                    width: 250px;
                    margin-top: 80px;
    
                    @media only screen and (max-width: $bp-xl) {
                        margin-right: 20px;
                        margin-left: 20px;

                        @media only screen and (max-width: 1050px) {
                            width: 220px;
                        }
                    }
    
                    @media only screen and (max-width: $bp-lg) {
                        width: 200px;
                    }
                    
                    &:nth-of-type(1),
                    &:nth-of-type(3){
                        margin-top: 225px;
                    }
           
    
                    &:nth-of-type(3n + 2):not(:last-of-type){
                        margin-top: -145px;
                    }
    
                    &:nth-of-type(2){
                        margin-top: 0 !important;
                    }
    
                    &:last-of-type{
                        order: 2;
                        margin-top: -145px;
    
                        @media only screen and (max-width: $bp-xl) {
                            margin-top: 80px;
                        }
                    }
                }
            }
        }

        .slider{
            &__pagination{
                margin-top: 20px;
            }
        }
    }

    .teacher-block{
        width: 100%;
    }

    .teachers-menu{
        position: relative;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;

        @media only screen and (max-width: $bp-xl){
            position: static;
            margin-right: auto;
        }
    }

    &--slider {
        @media only screen and (max-width: $bp-lg) {
            padding-top: 60px;
        }
    }

    &--slider .teachers-menu{
        position: relative;
        z-index: 2;
        top: auto;
        left: auto;
        position: absolute;
        bottom: 65px;
        right: -95px;

        @media only screen and (max-width: $bp-xl) {
            display: none;
        }
    }

    &--slider .teacher-block{
        width: 100%;
    }

    &--slider &__inner{
        position: relative;
        width: 100%;
        max-width: 930px;
        margin: 0 auto;

        @media only screen and (max-width: $bp-xl) {
            max-width: none;
        }
    }

    &--slider &__content{
        margin: 0 auto;
    }

    &--slider &__title{
        font-size: 130px;
        line-height: 120%;
        letter-spacing: 0.22px;
        color: $blue;
        display: inline-flex;
        flex-direction: column;
        font-weight: 800;
        width: 250px;
        margin-right: auto;

        span{
            font-weight: 700;
            font-size: 65px;
            line-height: 100%;
            margin-top: -25px;
        }

        @media only screen and (max-width: $bp-xl) {
            font-size: 110px;

            span{
                font-size: 58px;
            }
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 95px;
            margin-bottom: 40px;
            flex-direction: row;
            align-items: center;

            span{
                margin-left: 15px;
                margin-top: 0;
                font-size: 50px;
            }
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 85px;

            span{
                font-size: 40px;
            }
        }
    }

}